angular.module('PublishVersionCommentDetailsCtrl', ['ui.bootstrap']).controller('PublishVersionCommentDetailsController', ['$rootScope', '$scope', '$modalInstance', 'ObjectDiff', 'Forms', 'formId', 'comment', 'publishComments', function ($rootScope, $scope, $modalInstance, ObjectDiff, Forms, formId, comment, publishComments) {
  $scope.currentPage = 1;
  $scope.paginationSize = 5;

  $scope.comment = comment;

  $scope.filterBySelectedLanguage = function (comment) {
    return (
      // Old versions use two character language codes
      $scope.selectedLanguage.code.includes(comment.JSONFHIR.language)
    );
  };

  $scope.labels = {
    'fi-FI': 'Suomeksi (fi-FI)',
    'sv-FI': 'Ruotsiksi (sv-FI)',
    'en-GB': 'Englanniksi (en-GB)'
  };
  $scope.langs = $scope.comment.languages.map(function (lang) {
    return {
      label: $scope.labels[lang],
      code: lang
    };
  });

  $scope.selectedLanguage = $scope.langs.find(function (l) {
    return l.code.includes('fi-FI');
  }) || $scope.langs[0];

  $scope.allPublishComments = publishComments;
  $scope.allPublishCommentsOnSelectedLang = publishComments.filter($scope.filterBySelectedLanguage);
  const diffComment = publishComments.filter(function (el) {
    return el.ID !== comment.ID;
  }).shift();

  function setDiff() {
    $scope.diffedComment = ObjectDiff.diffOwnProperties($scope.comment.JSONFHIR, $scope.commentToBeDiffed.JSONFHIR);
  }

  function processDiffBetweenComments(diffCommentId) {
    $scope.diffCommentId = diffCommentId;

    if (!diffCommentId) {
      Forms.loadCurrentSavedVersionByFormId(formId, function (data) {
        $scope.commentToBeDiffed = { JSONFHIR: data };
        setDiff();
      }, { language: $scope.selectedLanguage.code });
    } else {
      $scope.commentToBeDiffed = $scope.allPublishCommentsOnSelectedLang.find(function (comment) {
        return comment.ID === $scope.diffCommentId;
      });
      setDiff();
    }
  }

  if (diffComment.ID) {
    processDiffBetweenComments(diffComment.ID);
  }

  $scope.$watch('selectedLanguage', function () {
    $scope.allPublishCommentsOnSelectedLang = $scope.allPublishComments.filter($scope.filterBySelectedLanguage);
    $scope.comment = $scope.allPublishCommentsOnSelectedLang.find(function (c) {
      return c.ID === $scope.comment.ID;
    });
    if ($scope.comment) {
      processDiffBetweenComments($scope.diffCommentId);
    }
  });

  $scope.handleDiff = function (commentToBeDiffed) {
    processDiffBetweenComments(commentToBeDiffed.ID);
  };

  $scope.handleCurrentSavedVersion = function () {
    processDiffBetweenComments(null);
  };

  $scope.handleDeprecate = function (deprecate, id) {
    Forms.deprecateFormVersion(deprecate)(id, function (_, status) {
      if (status < 400) {
        $modalInstance.close({ action: 'reload-versions' });
      }
    });
  };

  $scope.close = function () {
    $modalInstance.close({ action: 'close', payload: 'Closing version details' });
  };
}]);