// Controller for setting display logic to question that is linked to form.

angular.module('ModifyQuestionDisplayLogicModalCtrl', ['angularUtils.directives.dirPagination', 'ui.bootstrap']).controller('ModifyQuestionDisplayLogicModalController', ['$q', '$rootScope', '$scope', '$modalInstance', 'Questions', '$state', '$stateParams', '$timeout', function ($q, $rootScope, $scope, $modalInstance, Questions, $state, $stateParams, $timeout) {
  $scope.formid = $stateParams.formid;
  $scope.questionid = $rootScope.selectedLogicQuestion.ID;
  $scope.questionname = $rootScope.selectedLogicQuestion.NAME;

  $scope.questions = Questions.getDisplayQuestions;
  Questions.loadDisplayQuestionData($scope.formid, $scope.questionid);

  $scope.selectedquestion = '';
  $scope.selectedquestionid = '';
  $scope.answertype = '';
  $scope.answeroperation = '';
  $scope.answervalue = '';

  const answerArray = [];
  const answerArrayID = [];
  $scope.selectAnswer = function (answer) {
    if (jQuery.inArray(answer.ANSWER_TEXT, answerArray) == -1) {
      answerArray.push(answer.ANSWER_TEXT);
      answerArrayID.push(answer.ANSWER_ID);
    } else {
      const answerArrayWithIndex = answerArray.indexOf(answer.ANSWER_TEXT);
      const answerArrayWithIndexID = answerArrayID.indexOf(answer.ANSWER_ID);
      if (answerArrayWithIndex != -1) {
        answerArray.splice(answerArrayWithIndex, 1);
        answerArrayID.splice(answerArrayWithIndexID, 1);
      }
    }
  };

  $scope.ok = function () {
    $modalInstance.close();
  };
  $scope.cancel = function () {
    $modalInstance.dismiss('cancel');
  };

  $scope.SaveDisplayLogic = function () {
    $scope.answermultivalue = answerArray;
    $scope.answermultivalueID = answerArrayID;
    let checkMultiValue = 0;
    const displayinfo = {};
    displayinfo.VALUE = '';
    displayinfo.ANSWER_ID = '';
    displayinfo.FORM_ID = '';
    displayinfo.QUESTION_ID = '';
    displayinfo.ANSWER_QUESTION_ID = '';
    displayinfo.TYPE = '';
    displayinfo.LOGIC = '';
    displayinfo.VALUE = '';
    for (let i = 0; $scope.answermultivalue.length > i; i++) {
      checkMultiValue = 1;
      if (i > 0) {
        displayinfo.VALUE += '||';
        displayinfo.ANSWER_ID += '||';
      }
      displayinfo.VALUE += $scope.answermultivalue[i];
      displayinfo.ANSWER_ID += $scope.answermultivalueID[i];
      displayinfo.FORM_ID = $scope.formid;
      displayinfo.QUESTION_ID = $scope.questionid;
      displayinfo.ANSWER_QUESTION_ID = $scope.selectedquestionid;
      displayinfo.TYPE = $scope.answertype;
      displayinfo.LOGIC = $scope.answeroperation;
    }
    if (checkMultiValue == 0) {
      displayinfo.VALUE = $scope.answervalue;
      displayinfo.FORM_ID = $scope.formid;
      displayinfo.QUESTION_ID = $scope.questionid;
      displayinfo.ANSWER_QUESTION_ID = $scope.selectedquestionid;
      displayinfo.TYPE = $scope.answertype;
      displayinfo.LOGIC = $scope.answeroperation;
      Questions.createDisplayRule(displayinfo, function (status) {
        $modalInstance.close(displayinfo.QUESTION_ID);
      });
    } else {
      Questions.createDisplayRule(displayinfo, function (status) {
        $modalInstance.close(displayinfo.QUESTION_ID);
      });
    }
  };
}]);