// eslint-disable-next-line
var prefix = "/";

angular.module('form_app', ['AppRoutes',

// Config
'app.config',

// libs
'ngAnimate', 'ngTagsInput', 'pascalprecht.translate', 'textAngular', 'toastr', 'angular-loading-bar', 'ds.objectDiff',

// Controllers
'ConfirmCtrl', 'HeaderCtrl', 'LoginCtrl', 'FormListCtrl', 'QuestionListCtrl', 'QuestionDetailsCtrl', 'FormDetailsCtrl', 'FormPreviewCtrl', 'NewQuestionCtrl', 'FormQuestionsCtrl', 'LinkQuestionModalCtrl', 'NewQuestionModalCtrl', 'ModifyQuestionDisplayLogicModalCtrl', 'ModifyFormOrganizationsCtrl', 'ModifyQuestionOrganizationsCtrl', 'NoteFeedCtrl', 'NoteModalCtrl', 'DeleteConfirmationModalCtrl', 'NewFormCtrl', 'PublishVersionCommentCtrl', 'PublishVersionCommentDetailsCtrl',

// Directives
'ConfirmDirective', 'ConvertDirective',

// Services
'FormService', 'QuestionService', 'NoteService', 'EbmedsService', 'OrganizationService']).run(function (api_prefix, logout_page, userinfo_page, oda_preview_url, $rootScope) {
  $rootScope.api_prefix = api_prefix;
  $rootScope.logout_page = logout_page;
  $rootScope.userinfo_page = userinfo_page;
  $rootScope.oda_preview_url = oda_preview_url;
}).config(function (paginationTemplateProvider) {
  paginationTemplateProvider.setPath(prefix + 'view/dirPagination.tpl.html');
})
// Initialize the translations
// Angular-translate library is used for site translations
.config(['$translateProvider', function ($translateProvider) {
  $translateProvider.useLoader('translationLoader', {});
  $translateProvider.use('FI');
  $translateProvider.useSanitizeValueStrategy('escaped');
}])
// Used with TranslateProvider to load JSON language files
.factory('translationLoader', function ($http, $q) {
  return function (options) {
    const deferred = $q.defer();

    $http({
      method: 'GET',
      url: prefix + 'lang/locale-' + options.key + '.json'
    }).success(function (data) {
      deferred.resolve(data);
    }).error(function () {
      deferred.reject(options.key);
    });
    return deferred.promise;
  };
})
// Initialize an HTTP interceptor that moves the user to the login page
// if any HTTP request returns with a 401 unauthorized status
.config(['$httpProvider', function ($httpProvider) {
  // This is accomplished by pushing a new interceptor to the httpProvider
  $httpProvider.interceptors.push(function ($timeout, $q, $injector) {
    let $http;
    let $state;

    // this trick must be done so that we don't receive
    // `Uncaught Error: [$injector:cdep] Circular dependency found`
    $timeout(function () {
      $http = $injector.get('$http');
      $state = $injector.get('$state');
    });

    // IE cache issue starts
    // Initialize get if not there
    if (!$httpProvider.defaults.headers.get) {
      $httpProvider.defaults.headers.get = {};
    }

    // Answer edited to include suggestions from comments
    // because previous version of code introduced browser-related errors

    // disable IE ajax request caching
    $httpProvider.defaults.headers.get['If-Modified-Since'] = 'Mon, 26 Jul 1997 05:00:00 GMT';
    // extra
    $httpProvider.defaults.headers.get['Cache-Control'] = 'no-cache';
    $httpProvider.defaults.headers.get.Pragma = 'no-cache';
    // IE cache issue ends

    return {
      responseError(rejection) {
        const deferred = $q.defer();

        // In the case of a 401 status, throw the user to the login page if not already there
        if (rejection.status === 401 && $state.includes('login') === false) {
          $state.go('login');
        }

        deferred.reject(rejection);
        return deferred.promise;
      }
    };
  });
}])
// Custom settings for the toastr library
.config(function (toastrConfig) {
  angular.extend(toastrConfig, {
    autoDismiss: true,
    positionClass: 'toast-bottom-right',
    maxOpened: 2,
    newestOnTop: true,
    progressBar: true,
    timeOut: 5000,
    closeButton: true
  });
});