// Controller for the modal that is used to link an existing question to form

angular.module('LinkQuestionModalCtrl', ['angularUtils.directives.dirPagination', 'ui.bootstrap']).controller('LinkQuestionModalController', ['$scope', '$modalInstance', 'Questions', 'Forms', '$modal', '$stateParams', function ($scope, $modalInstance, Questions, Forms, $modal, $stateParams) {
  const formID = $stateParams.formid;
  $scope.questionListPageSize = 10;
  $scope.currentPage = 1;

  Questions.loadQuestions(function (status) {
    const allQuestions = Questions.getQuestions;
    Forms.loadQuestionsInForm(formID, function (data, status) {
      const formQuestions = data;
      for (var j = 0; formQuestions.length > j; j++) {
        if (allQuestions.map(function (e) {
          return e.ID;
        }).indexOf(formQuestions[j].ID) > -1) {
          // Remove questions that have already been added to this form.
          allQuestions.splice(allQuestions.map(function (e) {
            return e.ID;
          }).indexOf(formQuestions[j].ID), 1);
        }
      }
      for (var j = 0; allQuestions.length > j; j++) {
        if (allQuestions[j].QUESTIONTEXT) {
          allQuestions[j].QUESTIONTEXT = allQuestions[j].QUESTIONTEXT.replace(/<(?:.|\n)*?>/gm, '');
        }
      }
      $scope.questions = allQuestions;
    });
  });

  $scope.ok = function () {
    if ($scope.questionID) {
      $modalInstance.close($scope.questionID);
    }
  };
  $scope.cancel = function () {
    $modalInstance.dismiss('cancel');
  };
  // Opens a modal to link a new question to form
  $scope.openNewQuestionModal = function () {
    const modalInstance = $modal.open({
      size: 'lg',
      templateUrl: 'view/modal/newQuestionModal.html',
      backdrop: 'static',
      controller: 'NewQuestionModalController'
    });

    // User presses Save button
    modalInstance.result.then(function (questionID) {
      Forms.loadQuestionsInForm(formID, function (data, status) {
        $scope.questions = data;
        // use $scope.questions.length + 1 as the next position so the new question will be on the last position on the form
        Forms.linkQuestionToForm(formID, questionID, $scope.questions.length + 1, function (data, status) {
          $modalInstance.close('NEW_QUESTION_ADDED');
        });
      });
    });
  };
}]);