angular.module('ConfirmDirective', []).directive('confirm', function ($modal, $parse) {
  return {
    // So the link function is run before ngClick's, which has priority 0
    priority: -1,
    link: function (scope, element, attrs) {
      element.on('click', function (event) {
        // Don't run ngClick's handler
        event.stopImmediatePropagation();

        $modal.open({
          templateUrl: 'view/modal/confirm-modal.html',
          controller: 'ConfirmController',
          resolve: {
            message: function () {
              return attrs.confirm;
            }
          }
        }).result.then(function () {
          // Pass original click as '$event'
          $parse(attrs.ngClick)(scope, { $event: event });
        });
      });
    }
  };
});