/* Controller for adding and editing notes
*/
angular.module('NoteModalCtrl', ['ui.bootstrap']).controller('NoteModalController', ['$scope', '$modalInstance', 'modalTitle', 'noteData', function ($scope, $modalInstance, modalTitle, noteData) {
  $scope.title = modalTitle;
  $scope.note = {};

  if (noteData) {
    $scope.note.TEXT = noteData.TEXT;
    $scope.note.ID = noteData.ID;
  }

  $scope.ok = function () {
    if ($scope.note.TEXT) {
      $modalInstance.close($scope.note);
    }
  };
  $scope.cancel = function () {
    $modalInstance.dismiss('cancel');
  };
}]);