angular.module('QuestionService', []).factory('Questions', ['$http', '$rootScope', 'toastr', function ($http, $rootScope, toastr) {
  const api_prefix = $rootScope.api_prefix;

  const questions = [];
  const question = {};
  const answers = [];
  const displayquestionsdata = [];
  const displayquestionsanswerdata = [];
  const displayQuestions = { value: false };
  const questionsLoading = {
    value: false,
    answers: false,
    notes: false,
    answersSuccess: false,
    displayQuestions: false,
    question: false
  };
  // Question type can be chosen on question page
  // type: saved in the DB, text: shown to user, value: used for the radio buttons, description: shown in answer choices area
  const questionTypes = [{ type: 'YESNO', text: 'Kyll/ei' }, { type: 'NUMBER', text: 'Lukuarvo' }, { type: 'TEXT', text: 'Vapaa teksti (yksirivinen)' }, { type: 'TEXTAREA', text: 'Vapaa teksti (useampi rivi)' }, { type: 'SELECT', text: 'Monivalinta (valitse yksi vastaus)' }, { type: 'CHECK', text: 'Monivalinta (valitse useampi vastaus)' }, { type: 'DATE', text: 'Pivmr' }, { type: 'SUBTITLE', text: 'Vliotsikko' }, { type: 'FEEDBACK', text: 'Vlipalaute' }, { type: 'TERMINUS', text: 'Loppupalaute' }];

  const questionTypesEN = [{ type: 'YESNO', text: 'Yes/No' }, { type: 'NUMBER', text: 'Numeric' }, { type: 'TEXT', text: 'Free Text (one line)' }, { type: 'TEXTAREA', text: 'Free Text (multiple lines)' }, { type: 'SELECT', text: 'Select (choose one answer)' }, { type: 'CHECK', text: 'Select (choose multiple answers)' }, { type: 'DATE', text: 'Date' }, { type: 'SUBTITLE', text: 'Subtitle' }, { type: 'FEEDBACK', text: 'Feedback' }, { type: 'TERMINUS', text: 'Terminus' }];

  function loadCodeSystems(callback) {
    $http.get(api_prefix + 'api/code-systems').success(function (data) {
      callback(data);
    }).error(function (_, status) {
      toastr.error('Fetching data failed with status: ' + status);
    });
  }

  function loadSystemCodes(systemId, callback) {
    $http.get(api_prefix + 'api/code-systems/' + systemId + '/codes').success(function (data) {
      callback(data.items);
    }).error(function (_, status) {
      toastr.error('Fetching data failed with status: ' + status);
    });
  }

  function loadQuestions(callback) {
    questionsLoading.value = true;
    $http.get(api_prefix + 'api/questions').success(function (data, status) {
      // Angular copy so that the change is noticed in Controller and 2-way binding works
      angular.copy(data, questions);

      if (callback) {
        callback(status);
      }
    }).error(function (data, status) {
      // Show error message to user
      toastr.error('Fetching data failed with status: ' + status);

      if (callback) {
        callback(status);
      }
    }).finally(function () {
      questionsLoading.value = false;
    });
  }

  // Loads the question and its answers according to question ID
  function loadQuestion(id, callback) {
    questionsLoading.question = true;
    $http.get(api_prefix + 'api/questions/' + id).success(function (data, status) {
      angular.copy(data, question);

      if (callback) {
        callback(status);
      }
    }).error(function (data, status) {
      toastr.error('Fetching question data failed with status: ' + status);

      if (callback) {
        callback(status);
      }
    }).finally(function () {
      questionsLoading.question = false;
    });
  }

  // Extracts the language option from an options object
  function getLanguage(options) {
    return options && options.language || 'fi-FI';
  }

  // Loads all answer choices to question ID
  function loadAnswers(id, callback, options) {
    questionsLoading.answers = true;
    $http.get(api_prefix + 'api/questions/' + id + '/answers?lang=' + getLanguage(options)).success(function (data, status) {
      angular.copy(data, answers);

      if (callback) {
        callback(data, status);
      }
    }).error(function (data, status) {
      toastr.error('Fetching answer data failed with status: ' + status);

      if (callback) {
        callback(data, status);
      }
    }).finally(function () {
      questionsLoading.answers = false;
    });
  }

  // Saves a new question to the DB (not its answers)
  function createQuestion(questionInfo, callback) {
    questionInfo.CREATED_BY = $rootScope.loggedUser.id;

    $http.post(api_prefix + 'api/questions', questionInfo).success(function (data, status) {
      toastr.success('Question created');

      if (callback) {
        callback(status, data);
      }
    }).error(function (data, status) {
      console.log('Error: cannot create new question.');
      toastr.error('Creating question failed with status: ' + status);

      if (callback) {
        callback(status, data);
      }
    });
  }

  // Updates the question in DB
  function updateQuestion(questionInfo, answerInfo, callback) {
    const questionID = questionInfo.ID;
    questionInfo.EDITED_BY = $rootScope.loggedUser.id;

    // Update the question information
    $http.put(api_prefix + 'api/questions/' + questionID, questionInfo).success(function (data, status) {
      toastr.success('Question saved');

      if (callback) {
        callback(status);
      }
    }).error(function (data, status) {
      toastr.error('Updating question failed with status: ' + status);

      if (callback) {
        callback(status);
      }
    });
  }

  // Deletes a question from the database using DELETE with the question ID
  function deleteQuestion(id, callback) {
    $http.delete(api_prefix + 'api/questions/' + id).success(function (data, status) {
      toastr.success('Question removed');

      if (callback) {
        callback(status);
      }
    }).error(function (data, status) {
      toastr.error('Deleting question failed with status: ' + status);

      if (callback) {
        callback(status);
      }
    });
  }

  function deleteQuestionCode(id, callback) {
    $http.delete(api_prefix + 'api/questions/codes/' + id).success(callback).error(function (_, status) {
      toastr.error('Removing question code failed with status: ' + status);
    });
  }

  // Creates a new answer in DB for questionID
  function createAnswer(questionID, answerInfo, callback) {
    questionsLoading.answers = true;
    $http.post(api_prefix + 'api/questions/' + questionID + '/answer', answerInfo).success(function (data, status) {
      if (callback) {
        callback(status);
      }
    }).error(function (data, status) {
      toastr.error('Saving answer failed with status: ' + status);

      if (callback) {
        callback(status);
      }
    }).finally(function () {
      questionsLoading.answers = false;
    });
  }

  // Deletes an answer from the database
  function deleteAnswer(questionID, answerID, callback) {
    questionsLoading.answers = true;
    $http.delete(api_prefix + 'api/answers/' + answerID).success(function (data, status) {
      if (callback) {
        callback(status);
      }
    }).error(function (data, status) {
      toastr.error('Deleting answer failed with status: ' + status);

      if (callback) {
        callback(status);
      }
    }).finally(function () {
      questionsLoading.answers = false;
    });
  }

  // Updates answer to database
  function editAnswer(answer, callback) {
    questionsLoading.answers = true;
    $http.put(api_prefix + 'api/answers/' + answer.ID, answer).success(function (data, status) {
      if (callback) {
        callback(data, status);
      }
    }).error(function (data, status) {
      toastr.error('Editing answer failed with status: ' + status);

      if (callback) {
        callback(data, status);
      }
    }).finally(function () {
      questionsLoading.answers = false;
    });
  }

  function deleteAnswerCode(id, callback) {
    $http.delete(api_prefix + 'api/answers/codes/' + id).success(callback).error(function (_, status) {
      toastr.error('Removing answer code failed with status: ' + status);
    });
  }

  // Switches places between two answers
  function switchAnswers(index1, index2, callback) {
    questionsLoading.answers = true;
    if (index1 >= 0 && index1 < answers.length && index2 >= 0 && index2 < answers.length) {
      const temp = answers[index1];
      answers[index1] = answers[index2];
      answers[index2] = temp;

      $http.put(api_prefix + 'api/answers/' + answers[index1].ID + '/switch/' + answers[index2].ID).success(function (data, status) {
        if (callback) {
          callback(status);
        }
      }).error(function (data, status) {
        toastr.error('Editing answer failed with status: ' + status);

        if (callback) {
          callback(status);
        }
      }).finally(function () {
        questionsLoading.answers = false;
      });
    } else {
      // Invalid parameters
      toastr.error('Editing answer failed with status: ' + status);
      callback(500);
      questionsLoading.answers = false;
    }
  }

  // Returns names of forms that are linked to a question
  function loadLinkedForms(questionID, callback) {
    questionsLoading.value = true;
    $http.get(api_prefix + 'api/questions/' + questionID + '/forms').success(function (data, status) {
      if (callback) {
        callback(data, status);
      }
    }).error(function (data, status) {
      toastr.error('Loading linked forms failed with status: ' + status);

      if (callback) {
        callback(data, status);
      }
    }).finally(function () {
      questionsLoading.value = false;
    });
  }

  // Loads all the notes in question by question id
  function loadNotesInQuestion(questionID, callback) {
    questionsLoading.notes = true;
    $http.get(api_prefix + 'api/questions/' + questionID + '/notes').success(function (data, status) {
      if (callback) {
        callback(data, status);
      }
    }).error(function (data, status) {
      // Show error message to user
      toastr.error('Fetching data failed with status: ' + status);

      if (callback) {
        callback(data, status);
      }
    }).finally(function () {
      questionsLoading.notes = false;
    });
  }

  // Sets questions view hidden/displayed
  function setStatus(status) {
    if (status == 'show') {
      displayQuestions.value = true;
    } else {
      displayQuestions.value = false;
    }
  }

  // Loads all display rules to question ID and form ID
  function loadDisplayRules(formid, questionid, SetLanguage, callback) {
    questionsLoading.answers = true;
    $http.get(api_prefix + 'api/display_question/' + formid + '/' + questionid).success(function (data, status) {
      if (data) {
        for (let i = 0; i < data.length; i++) {
          if (data[i].TYPE == 'YESNO') {
            if (data[i].VALUE == 'yes' && SetLanguage == 'FI') {
              data[i].VALUE = 'kyll';
            }
            if (data[i].VALUE == 'no' && SetLanguage == 'FI') {
              data[i].VALUE = 'ei';
            }
          }
          if (data[i].TYPE == 'TEXT' || data[i].TYPE == 'TEXTAREA') {
            if (data[i].LOGIC == 'equals' && SetLanguage == 'FI') {
              data[i].LOGIC = 'on yht kuin';
            }
            if (data[i].LOGIC == 'starts with' && SetLanguage == 'FI') {
              data[i].LOGIC = 'alkaa tekstill';
            }
            if (data[i].LOGIC == 'contains' && SetLanguage == 'FI') {
              data[i].LOGIC = 'sislt tekstin';
            }
          }
          if (data[i].TYPE == 'NUMBER') {
            if (data[i].LOGIC == 'equals' && SetLanguage == 'FI') {
              data[i].LOGIC = 'yht kuin';
            }
            if (data[i].LOGIC == 'greater than' && SetLanguage == 'FI') {
              data[i].LOGIC = 'suurempi kuin';
            }
            if (data[i].LOGIC == 'less than' && SetLanguage == 'FI') {
              data[i].LOGIC = 'pienempi kuin';
            }
          }
        }
      }
      if (callback) {
        callback(data, status);
      }
    }).error(function (data, status) {
      toastr.error('Fetching display logic failed with status: ' + status);

      if (callback) {
        callback(data, status);
      }
    }).finally(function () {});
  }

  // Loads all display rules to question ID and form ID
  function loadDisplayRulesAll(formid, SetLanguage, callback) {
    questionsLoading.answers = true;
    $http.get(api_prefix + 'api/display_question/' + formid).success(function (data, status) {
      if (data) {
        for (let i = 0; i < data.length; i++) {
          if (data[i].TYPE == 'YESNO') {
            if (data[i].VALUE == 'yes' && SetLanguage == 'FI') {
              data[i].VALUE = 'kyll';
            }
            if (data[i].VALUE == 'no' && SetLanguage == 'FI') {
              data[i].VALUE = 'ei';
            }
          }
          if (data[i].TYPE == 'TEXT' || data[i].TYPE == 'TEXTAREA') {
            if (data[i].LOGIC == 'equals' && SetLanguage == 'FI') {
              data[i].LOGIC = 'on yht kuin';
            }
            if (data[i].LOGIC == 'starts with' && SetLanguage == 'FI') {
              data[i].LOGIC = 'alkaa tekstill';
            }
            if (data[i].LOGIC == 'contains' && SetLanguage == 'FI') {
              data[i].LOGIC = 'sislt tekstin';
            }
          }
          if (data[i].TYPE == 'NUMBER') {
            if (data[i].LOGIC == 'equals' && SetLanguage == 'FI') {
              data[i].LOGIC = 'yht kuin';
            }
            if (data[i].LOGIC == 'greater than' && SetLanguage == 'FI') {
              data[i].LOGIC = 'suurempi kuin';
            }
            if (data[i].LOGIC == 'less than' && SetLanguage == 'FI') {
              data[i].LOGIC = 'pienempi kuin';
            }
          }
        }
      }
      if (callback) {
        callback(data, status);
      }
    }).error(function (data, status) {
      toastr.error('Fetching display logic failed with status: ' + status);
      if (callback) {
        callback(data, status);
      }
    }).finally(function () {});
  }

  // Loads all display logic possibilities for question in a form. Data for questions.
  function loadDisplayQuestionData(formid, questionid, callback) {
    questionsLoading.displayQuestions = true;
    $http.get(api_prefix + 'api/display_question_data/' + formid + '/' + questionid).success(function (data, status) {
      // Angular copy so that the change is noticed in Controller and 2-way binding works
      angular.copy(data, displayquestionsdata);
      for (let i = 0; i < data.length; i++) {
        loadDisplayQuestionAnswerData(displayquestionsdata[i].QUESTION_ID, displayquestionsdata, i);
      }
      if (callback) {
        callback(data, status);
      }
    }).error(function (data, status) {
      toastr.error('Fetching display logic questions failed with status: ' + status);
      if (callback) {
        callback(data, status);
      }
    }).finally(function () {
      questionsLoading.displayQuestions = false;
    });
  }

  // Loads all display logic possibilities for question in a form. Data for answers.
  function loadDisplayQuestionAnswerData(questionid, questiondata, index, callback) {
    $http.get(api_prefix + 'api/display_question_answer_data/' + questionid).success(function (data, status) {
      questiondata[index].answers = data;
      if (callback) {
        callback(data, status);
      }
    }).error(function (data, status) {
      toastr.error('Fetching display logic answers failed with status: ' + status);

      if (callback) {
        callback(data, status);
      }
    }).finally(function () {});
  }

  // Creates a new display rule for question if form.
  function createDisplayRule(displayInfo, callback) {
    $http.post(api_prefix + 'api/display_question', displayInfo).success(function (data, status) {
      if (callback) {
        callback(status);
      }
    }).error(function (data, status) {
      toastr.error('Saving display logic failed with status: ' + status);

      if (callback) {
        callback(status);
      }
    }).finally(function () {});
  }

  // Saves operator to existing display rule
  function saveDisplayRuleOperator(operator, operatorid, callback) {
    $http.post(api_prefix + 'api/display_question_operator/' + operatorid, operator).success(function (data, status) {
      if (callback) {
        callback(status);
      }
    }).error(function (data, status) {
      toastr.error('Saving operator for display logic failed with status: ' + status);

      if (callback) {
        callback(status);
      }
    }).finally(function () {});
  }

  // Deletes display rule from the database
  function removeDisplayLogic(logicID, callback) {
    $http.delete(api_prefix + 'api/display_question/' + logicID).success(function (data, status) {
      if (callback) {
        callback(status);
      }
    }).error(function (data, status) {
      toastr.error('Removing display logic failed with status: ' + status);

      if (callback) {
        callback(status);
      }
    }).finally(function () {});
  }

  return {
    getQuestions: questions,
    getQuestion: question,
    getQuestionTypes: questionTypes,
    getQuestionTypesEN: questionTypesEN,
    getAnswers: answers,
    setStatus,
    getDisplayStatus: displayQuestions,
    loadCodeSystems,
    loadSystemCodes,
    loadQuestions,
    loadQuestion,
    loadAnswers,
    createQuestion,
    updateQuestion,
    deleteQuestion,
    deleteQuestionCode,
    deleteAnswer,
    deleteAnswerCode,
    questionsLoading,
    createAnswer,
    switchAnswers,
    loadLinkedForms,
    loadNotesInQuestion,
    loadDisplayRules,
    loadDisplayRulesAll,
    loadDisplayQuestionData,
    getDisplayQuestions: displayquestionsdata,
    loadDisplayQuestionAnswerData,
    getDisplayAnswerQuestions: displayquestionsanswerdata,
    createDisplayRule,
    saveDisplayRuleOperator,
    removeDisplayLogic,
    editAnswer
  };
}]);