angular.module('PublishVersionCommentCtrl', ['ui.bootstrap']).controller('PublishVersionCommentController', ['$rootScope', '$scope', '$modalInstance', 'publishModalForm', 'languages', function ($rootScope, $scope, $modalInstance, publishModalForm, languages) {
  $scope.publishForm = publishModalForm;
  publishModalForm.languages = languages.reduce(function (prev, current) {
    return Object.assign({}, prev, { [current]: true });
  }, {});
  $scope.publish = function () {
    const isCommentField = !!$scope.publishModalForm.comment.$viewValue.trim().length > 0;
    if (isCommentField) {
      $modalInstance.close({ action: 'publish', payload: $scope.publishForm });
    }
  };

  $scope.cancel = function () {
    $modalInstance.dismiss({ action: 'cancel', payload: $scope.publishForm });
  };
}]);