// This service is used to handle notes that are written by users for forms and questions

angular.module('NoteService', []).factory('Notes', ['$http', '$rootScope', 'toastr', function ($http, $rootScope, toastr) {
  const api_prefix = $rootScope.api_prefix;

  const loading = { note: false };

  function saveNewNote(note, callback) {
    note.CREATED_BY = $rootScope.loggedUser.id;

    loading.note = true;
    $http.post(api_prefix + 'api/notes', note).success(function (data, status) {
      toastr.success('Note added');

      if (callback) {
        callback(status);
      }
    }).error(function (data, status) {
      toastr.error('Adding not failed with status: ' + status);

      if (callback) {
        callback(status);
      }
    }).finally(function (data, status) {
      loading.note = false;
    });
  }

  function editNote(note, callback) {
    loading.note = true;
    $http.put(api_prefix + 'api/notes/' + note.ID, note).success(function (data, status) {
      toastr.success('Note edited');

      if (callback) {
        callback(status);
      }
    }).error(function (data, status) {
      toastr.error('Editing note failed with status: ' + status);

      if (callback) {
        callback(status);
      }
    }).finally(function (data, status) {
      loading.note = false;
    });
  }

  function deleteNote(noteID, callback) {
    loading.note = true;
    $http.delete(api_prefix + 'api/notes/' + noteID).success(function (data, status) {
      toastr.success('Note deleted');

      if (callback) {
        callback(status);
      }
    }).error(function (data, status) {
      toastr.error('Deleting note failed with status: ' + status);

      if (callback) {
        callback(status);
      }
    }).finally(function (data, status) {
      loading.note = false;
    });
  }

  return {
    saveNewNote,
    editNote,
    deleteNote,
    loading
  };
}]);