angular.module('ConvertDirective', []).directive('convertToNumber', function () {
  return {
    require: 'ngModel',
    link(scope, element, attrs, ngModel) {
      ngModel.$parsers.push(function (val) {
        return parseInt(val, 10);
      });
      ngModel.$formatters.push(function (val) {
        return '' + val;
      });
    }
  };
});