// Controller for setting display logic to question that is linked to form.

angular.module('ModifyFormOrganizationsCtrl', ['angularUtils.directives.dirPagination', 'ui.bootstrap']).controller('ModifyFormOrganizationsController', ['$q', '$rootScope', '$scope', '$modalInstance', 'Questions', '$state', '$stateParams', '$timeout', 'Organization', 'formData', function ($q, $rootScope, $scope, $modalInstance, Questions, $state, $stateParams, $timeout, Organization, formData) {
  Organization.loadOrganizations(function (data, status) {
    $scope.organizations = data;
    for (let j = 0; formData.ORGANIZATIONS.length > j; j++) {
      for (let i = 0; $scope.organizations.length > i; i++) {
        if ($scope.organizations[i].ID == formData.ORGANIZATIONS[j].ORG_ID || $scope.organizations[i].STATUS == true) {
          $scope.organizations[i].STATUS = true;
        } else {
          $scope.organizations[i].STATUS = false;
        }
      }
    }
  });
  $scope.ok = function () {
    formData.ORGANIZATIONS = [];
    for (let i = 0; $scope.organizations.length > i; i++) {
      if ($scope.organizations[i].STATUS == true) {
        formData.ORGANIZATIONS.push({
          DELETED: null,
          FORM_ID: formData.ID,
          ID: $scope.organizations[i].ID,
          NAME: $scope.organizations[i].NAME,
          ORG_ID: $scope.organizations[i].ID
        });
      }
    }
    $modalInstance.close(formData);
  };
  $scope.cancel = function () {
    $modalInstance.dismiss('cancel');
  };
}]);