// Controller for setting display logic to question that is linked to form.

angular.module('ModifyQuestionOrganizationsCtrl', ['angularUtils.directives.dirPagination', 'ui.bootstrap']).controller('ModifyQuestionOrganizationsController', ['$q', '$rootScope', '$scope', '$modalInstance', 'Questions', '$state', '$stateParams', '$timeout', 'Organization', 'questionData', function ($q, $rootScope, $scope, $modalInstance, Questions, $state, $stateParams, $timeout, Organization, questionData) {
  Organization.loadOrganizations(function (data, status) {
    $scope.organizations = data;
    for (let j = 0; questionData.ORGANIZATIONS.length > j; j++) {
      for (let i = 0; $scope.organizations.length > i; i++) {
        if ($scope.organizations[i].ID == questionData.ORGANIZATIONS[j].ORG_ID || $scope.organizations[i].STATUS == true) {
          $scope.organizations[i].STATUS = true;
        } else {
          $scope.organizations[i].STATUS = false;
        }
      }
    }
  });
  $scope.ok = function () {
    questionData.ORGANIZATIONS = [];
    for (let i = 0; $scope.organizations.length > i; i++) {
      if ($scope.organizations[i].STATUS == true) {
        questionData.ORGANIZATIONS.push({
          DELETED: null,
          FORM_ID: questionData.ID,
          ID: $scope.organizations[i].ID,
          NAME: $scope.organizations[i].NAME,
          ORG_ID: $scope.organizations[i].ID
        });
      }
    }
    $modalInstance.close(questionData);
  };
  $scope.cancel = function () {
    $modalInstance.dismiss('cancel');
  };
}]);