angular.module('FormDetailsCtrl', ['angularUtils.directives.dirPagination', 'ui.bootstrap']).controller('FormDetailsController', ['$scope', '$stateParams', 'Forms', '$window', '$filter', '$translate', '$rootScope', '$location', '$modal', 'Organization', 'toastr', function ($scope, $stateParams, Forms, $window, $filter, $translate, $rootScope, $location, $modal, Organization, toastr) {
  $scope.formID = $stateParams.formid;
  $scope.readonlymode = true;
  $scope.publishDraft = true;
  $scope.readonlymodePublish = true;
  $scope.odaerror = '';
  $scope.publishModalForm = { id: null, comment: '' };
  let savedOrganizations = [];
  const $translateVar = $filter('translate');
  $scope.disabled = false;
  $scope.disabled2 = true;
  $scope.form = {
    ID: $scope.formID
  };
  $scope.oldForm = {
    ID: $scope.formID
  };
  $scope.categories = [{ categoryName: 'Oirearviokysely', categoryValue: 'symptom' }, { categoryName: 'Terveystarkastus', categoryValue: 'health-check' }, { categoryName: 'Pitkaikaissairaudet', categoryValue: 'chronic-disease' }];
  $scope.disabledValidation = true;

  $scope.disabledApproval = true;

  $scope.showOdaPreview = false;
  $scope.showFocalPreview = false;

  $scope.languages = [];
  $scope.focalUrl = '';

  Forms.loadFormByID($scope.formID, function (data, status) {
    const dataStatus = data.STATUS;
    Organization.loadFormOrganizations(function (data2, status2) {
      $scope.organizationList = '';
      $scope.form.ORGANIZATIONS = [];
      for (let i = 0; data2.length > i; i++) {
        if (data2[i].FORM_ID == $scope.formID) {
          $scope.form.ORGANIZATIONS.push(data2[i]);
          if ($scope.organizationList.length > 0) {
            $scope.organizationList += ', ';
          }
          $scope.organizationList += data2[i].NAME;
          if (data2[i].ORG_ID === '81') {
            $scope.showOdaPreview = true;
          }
          if (data2[i].ORG_ID === '105') {
            $scope.showFocalPreview = true;
            $scope.focalUrl = "";
          }
          if ($rootScope.loggedUser.organization == data2[i].ORG_ID && $rootScope.loggedUser.rightlevel == 2) {
            $scope.readonlymode = false;
          }
        }
      }
      savedOrganizations = $scope.form.ORGANIZATIONS;
      if ($rootScope.loggedUser) {

        if ($rootScope.loggedUser.rightlevel == 3) {
          $scope.readonlymode = false;
          if (dataStatus == 'Production') {
            $scope.readonlymodePublish = false;
          }
          if (dataStatus == 'Draft' && savedOrganizations.find(function (o) {
            return o.NAME === "FOCAL";
          })) {
            $scope.publishDraft = false;
          }
        }
      }

      $scope.oldForm.ORGANIZATIONS = savedOrganizations;
    });
    $scope.form = data;
    const previewLink = 'https://forms.ebmedscloud.org/questionnaire';
    $scope.previewLink = {
      url: previewLink + '/' + (data.SLUG || '<path>')
    };
    $scope.updatePreviewLink = function (slug) {
      $scope.previewLink.url = previewLink + '/' + slug;
    };
    $scope.oldForm = angular.copy(data);
    // adding link to scope
    $scope.rulelink = '';
    $scope.rulelinkExists = false;
    if ($scope.form.URL_LINK) {
      if ($scope.form.URL_LINK.length > 0) {
        if ($scope.form.URL_LINK.substr(0, 7) == 'http://' || $scope.form.URL_LINK.substr(0, 8) == 'https://') {
          $scope.rulelink = $scope.form.URL_LINK;
          $scope.rulelinkExists = true;
        } else {
          $scope.rulelink = 'http://' + $scope.form.URL_LINK;
          $scope.rulelinkExists = true;
        }
      }
    }

    $scope.formDetailsTitle = $translateVar('FORM_TITLE_1') + ' "' + $scope.form.NAME + '" ' + $translateVar('FORM_TITLE_2');
    // setting up dynamic status options based on form status from db.
    const staticStatus = $scope.form.STATUS;
    if (staticStatus == 'Draft') {
      $scope.statuses = [{
        statusName: $translateVar('DRAFT'),
        statusValue: 'Draft'
      }, {
        statusName: $translateVar('VALIDATION_PENDING'),
        statusValue: 'Validation Pending'
      }];
    }
    if (staticStatus == 'Validation Pending') {
      $scope.statuses = [{
        statusName: $translateVar('VALIDATION_PENDING'),
        statusValue: 'Validation Pending'
      }, {
        statusName: $translateVar('PRODUCTION'),
        statusValue: 'Production'
      }, { statusName: $translateVar('DISCARDED'), statusValue: 'Discarded' }];
    }
    if (staticStatus == 'Production') {
      $scope.statuses = [{
        statusName: $translateVar('PRODUCTION'),
        statusValue: 'Production'
      }, { statusName: $translateVar('DRAFT'), statusValue: 'Draft' }, { statusName: $translateVar('DISCARDED'), statusValue: 'Discarded' }];
    }
    if (staticStatus == 'Discarded') {
      $scope.statuses = [{
        statusName: $translateVar('DISCARDED'),
        statusValue: 'Discarded'
      }, { statusName: $translateVar('DRAFT'), statusValue: 'Draft' }];
    }

    // Validated checkbox must be checked if validation exists
    if ($scope.form.VALIDATED) {
      $scope.validated = true;
    }
  });

  $scope.editOrganizations = function (form) {
    const modalInstance = $modal.open({
      animation: true,
      size: 'lg',
      templateUrl: 'view/modal/modifyFormOrganizationsModal.html',
      backdrop: 'static',
      controller: 'ModifyFormOrganizationsController',
      resolve: {
        formData() {
          return form;
        }
      }
    });

    modalInstance.result.then(function (newFormData) {
      $scope.form = newFormData;
      $scope.organizationList = '';
      for (let i = 0; $scope.form.ORGANIZATIONS.length > i; i++) {
        if ($scope.organizationList.length > 0) {
          $scope.organizationList += ', ';
        }
        $scope.organizationList += $scope.form.ORGANIZATIONS[i].NAME;
      }
    });
  };

  // Defaults publish languages to previous version
  Forms.loadPublishCommentByFormId($stateParams.formid, function (versions) {
    if (versions.length > 0) {
      $scope.languages = versions[0].languages;
    } else {
      $scope.languages = ['en-GB', 'fi-FI', 'sv-FI'];
    }
  });

  const saveDraft = function () {
    Forms.PublishSaveFormVersion($scope.formID, $scope.form.SCRIPT_ID, {
      status: 'draft',
      comment: null,
      languages: { "fi-FI": true }
    }, function (data, status) {
      if (data && data.error) {
        return;
      }

      $rootScope.$emit('saved-publish-comment');
    });
  };

  const publish = function () {
    const publishModalInstance = $modal.open({
      animation: true,
      size: 'lg',
      templateUrl: 'view/modal/publish-version-comment-modal.html',
      controller: 'PublishVersionCommentController',
      backdrop: 'static',
      resolve: {
        publishModalForm: function () {
          return $scope.publishModalForm;
        },
        languages: function () {
          return $scope.languages;
        }
      }
    });

    publishModalInstance.result.then(function (modal) {
      if (modal.action === 'publish') {
        Forms.PublishSaveFormVersion($scope.formID, $scope.form.SCRIPT_ID, {
          comment: modal.payload.comment,
          languages: modal.payload.languages,
          status: 'Production'
        }, function (data, status) {
          if (data && data.error) {
            return;
          }

          if (data) {
            const odaErrorText = data.odaerror.filter(function (el) {
              return el.severity === 'error';
            }).reduce(function (acc, el) {
              return el.location ? acc + ' ' + el.location[0] + ':\n ' + el.diagnostics + '\n\n' : acc + ' ODA2 error:\n ' + el.diagnostics + '\n\n';
            }, '');
            $scope.odaerror = '' + $translateVar('ODA_ERROR') + odaErrorText;
          }
          $rootScope.$emit('saved-publish-comment');
        });
      }
    });
  };

  const previewOda = function () {
    Forms.PreviewFormVersion($scope.formID, $scope.form.SCRIPT_ID, function (data, status) {
      let odaErrorText = '';
      if (data.odaerror) {
        for (let i = 0; data.odaerror.length > i; i++) {
          if (data.odaerror[i].location) {
            odaErrorText += data.odaerror[i].location[0] + ':\n' + data.odaerror[i].diagnostics + '\n\n';
          } else {
            odaErrorText += 'ODA2 warning/error:\n' + data.odaerror[i].diagnostics + '\n\n';
          }
        }
        $scope.odaerror = $translateVar('ODA_ERROR') + odaErrorText;
      }
      if (data.oda_preview_id) {
        $window.location.href = $rootScope.oda_preview_url + $scope.formID + '?lang=' + $rootScope.previewLanguage.displayLanguage.toLowerCase();
      }
    }, { language: $rootScope.previewLanguage.dataLanguage });
  };

  const validate = function () {
    Forms.ValidateFormVersion($scope.formID, $scope.form.SCRIPT_ID, function (data, status) {
      let odaErrorText = '';
      if (data.issues) {
        for (let i = 0; data.issues.length > i; i++) {
          if (data.issues[i]) {
            odaErrorText += data.issues[i].diagnostics + '\n\n';
          } else {
            odaErrorText += 'ODA2 warning/error:\n' + data.issues[i].diagnostics + '\n\n';
          }
        }
        $scope.odaerror = $translateVar('ODA_ERROR') + odaErrorText;
      } else {
        $scope.odaerror = '';
      }
    }, { language: $rootScope.previewLanguage.dataLanguage });
  };

  const previewInternal = function () {
    $location.path('/formpreview/' + $scope.formID);
  };

  $scope.selectPreviewLang = function (toLang) {
    const previewLanguages = {
      FI: { displayLanguage: 'FI', dataLanguage: 'fi-FI' },
      SV: { displayLanguage: 'SV', dataLanguage: 'sv-FI' },
      EN: { displayLanguage: 'EN', dataLanguage: 'en-GB' }
    };

    $rootScope.previewLanguage = previewLanguages[toLang] || previewLanguages.FI;
  };

  $scope.handlePreview = function (previewType) {
    if (Forms.hasChanged($scope.form, $scope.oldForm)) {
      toastr.error('Form modified. You must save form before previewing.');
      return;
    }

    const types = {
      preview: previewInternal,
      previewOda,
      validate
    };
    types[previewType]();
  };

  $scope.handlePublish = function () {
    if (Forms.hasChanged($scope.form, $scope.oldForm)) {
      toastr.error('Form modified. You must save form before publishing.');
      return;
    }
    if ($rootScope.questionsWithIllegalDisplayRules.length > 0) {
      toastr.error('Illegal display logic AND/OR combinations. Cannot publish.');
      return;
    }
    publish();
  };

  $scope.handleSave = function () {
    console.log(' ~ file: formDetailsController.js:367 ~ Forms.editForm ~ $scope.form:', $scope.form);
    if ($scope.formForm.$valid === false) {
      toastr.error('Form contains errors - cannot proceed');
      return;
    }
    if (Forms.hasChanged($scope.form, $scope.oldForm)) {
      Forms.editForm($scope.form, function (data, status) {
        if (status >= 400) {
          toastr.error('Saving form was unsuccessful (status: ' + status + ')');
          return;
        }
        $scope.oldForm = angular.copy($scope.form);
      });
      saveDraft();
    } else {
      toastr.success('Form already up-to-date - no saving.');
    }
  };

  $scope.loading = Forms.loading;

  // Check if form details were changed;
  // if so, open confirmation modal window
  $scope.goBack = function () {
    if (Forms.hasChanged($scope.form, $scope.oldForm)) {
      const modalInstance = $modal.open({
        size: 'sm',
        templateUrl: 'view/modal/leave-page-modal.html',
        backdrop: 'static',
        controller: function ($scope, $modalInstance) {
          $scope.ok = function () {
            $modalInstance.close();
          };

          $scope.cancel = function () {
            $modalInstance.dismiss('cancel');
          };
        }
      });
      modalInstance.result.then(function () {
        window.history.back();
      });
    } else {
      window.history.back();
    }
  };
}]);