angular.module('EbmedsService', []).factory('Ebmeds', ['$http', '$rootScope', '$filter', 'toastr', function ($http, $rootScope, $filter, toastr) {
  const api_prefix = $rootScope.api_prefix;
  // Who you gonna call? Ghostbu...EBMeDS!!!
  function callEBMeDS(form, callback) {
    $http.post(api_prefix + 'api/ebmeds', form).success(function (data, status) {
      if (callback) {
        callback(data, status);
      }
    }).error(function (data, status) {
      // Show error message to user
      toastr.error('Fetching data failed with status: ' + status);
      if (callback) {
        callback(data, status);
      }
    }).finally(function () {});
  }

  return {
    callEBMeDS
  };
}]);