angular.module('FormQuestionsCtrl', ['angularUtils.directives.dirPagination', 'ui.bootstrap']).controller('FormQuestionsController', ['$rootScope', '$scope', '$stateParams', 'Forms', '$modal', 'Questions', '$http', 'toastr', '$filter', '$translate', 'Organization', function ($rootScope, $scope, $stateParams, Forms, $modal, Questions, $http, toastr, $filter, $translate, Organization) {
  const formID = $stateParams.formid;
  const $translateVar = $filter('translate');
  $scope.questions = [];
  $scope.groupQuestions = [];
  $scope.disabled = false;
  $scope.disabled2 = true;
  $scope.loading = Forms.loading;
  $scope.selectedQuestion = { ID: undefined };
  $scope.readonlymode = true;
  $scope.showSortControls = false;

  $rootScope.questionsWithIllegalDisplayRules = [];

  if ($rootScope.loggedUser) {
    if ($rootScope.loggedUser.rightlevel == 3) {
      $scope.readonlymode = false;
    }
  }
  if ($translate.use() == 'FI') {
    $scope.questionTypes = Questions.getQuestionTypes;
  }
  if ($translate.use() == 'EN') {
    $scope.questionTypes = Questions.getQuestionTypesEN;
  }

  const checkForIllegalDisplayRules = function (question) {
    const existing = $rootScope.questionsWithIllegalDisplayRules.findIndex(function (q) {
      return q.ID === question.ID;
    });

    if (existing >= 0) {
      $rootScope.questionsWithIllegalDisplayRules.splice(existing, 1);
    }

    if (question.DISPLAYRULES.length <= 1) {
      return undefined; // no combinator operators for a single rule
    }

    const operator = question.DISPLAYRULES[1].OPERATOR;

    if (question.DISPLAYRULES.slice(1).some(function (displayRule) {
      return displayRule.OPERATOR !== operator;
    })) {
      $rootScope.questionsWithIllegalDisplayRules.push(question);
    }
  };

  const loadQuestions = function () {
    $rootScope.questionsWithIllegalDisplayRules = [];

    Forms.loadQuestionsInForm(formID, function (data, status) {
      $scope.questions = data;
      $scope.groupQuestions = data.filter(function (q) {
        return q.TYPE === 'SUBTITLE';
      });

      Organization.loadFormOrganizations(function (data2, status2) {
        $scope.organizationList = '';
        $scope.ORGANIZATIONS = (data2 || []).filter(function (o) {
          return o.FORM_ID === formID;
        });
        $scope.organizationList = $scope.ORGANIZATIONS.map(function (o) {
          return o.NAME;
        }).join(', ');

        if ($rootScope.loggedUser.rightlevel === 2 && $scope.ORGANIZATIONS.find(function (o) {
          return o.ORG_ID === $rootScope.loggedUser.organization;
        })) {
          $scope.readonlymode = false;
        }
      });

      Questions.loadDisplayRulesAll(formID, $translate.use(), function (data, status) {
        $scope.questions.forEach(function (question) {
          question.DISPLAYRULES = (data || []).filter(function (logic) {
            return logic.QUESTION_ID === question.ID;
          });

          checkForIllegalDisplayRules(question);
        });
      });

      // Fetch answers and display logics for questions
      $scope.questions.forEach(function (question) {
        if (question.TYPE == 'CHECK' || question.TYPE == 'SELECT' || question.TYPE == 'NUMBER') {
          Questions.loadAnswers(question.ID, function (data, status) {
            question.ANSWERS = data;
            setNONE();
          });
        } else {
          question.ANSWERS = [];
        }
        function setNONE() {
          if (question.NONE_ANSWER == 'Y' && (question.TYPE == 'SELECT' || question.TYPE == 'CHECK')) {
            question.ANSWERS.push({
              ID: -1,
              ANSWER_TEXT: $translateVar('NONE_OF_THE_ABOVE')
            });
          }
        }
      });
    });
  };

  loadQuestions();

  $scope.setOptional = function (question) {
    question.FORM_ID = formID;
    question.OPTIONAL = question.OPTIONAL === '1' ? '0' : '1';
    Forms.setLinkedQuestionOptions(question);
  };

  $scope.setReadOnly = function (question) {
    question.FORM_ID = formID;
    question.READ_ONLY = question.READ_ONLY === '1' ? '0' : '1';
    Forms.setLinkedQuestionOptions(question);
  };

  $scope.expandAll = function () {
    for (let i = 0; i < $scope.questions.length; i++) {
      $scope.questions[i].expanded = true;
    }
  };

  $scope.collapseAll = function () {
    for (let i = 0; i < $scope.questions.length; i++) {
      $scope.questions[i].expanded = false;
    }
  };

  $scope.showQuestionAndDisplayLogic = function (question) {
    question.expanded = !question.expanded;
  };

  $scope.toggleSortControl = function () {
    $scope.showSortControls = !$scope.showSortControls;
  };

  const swapQuestions = function (a, b) {
    const questions = [$scope.questions[a], $scope.questions[b]];

    Forms.swapQuestionPositionsInForm(formID, questions[0].ID, questions[1].ID, function () {
      const positions = [questions[0].POSITION, questions[1].POSITION];

      questions[0].POSITION = positions[1];
      questions[1].POSITION = positions[0];

      $scope.questions[a] = questions[1];
      $scope.questions[b] = questions[0];
    });
  };

  $scope.setQuestionGroup = function (question) {
    Forms.setQuestionGroup(formID, question.ID, question.GROUP_QUESTION || null, function () {});
  };

  $scope.getQuestionGroupName = function (question) {
    const group = question.GROUP_QUESTION ? $scope.groupQuestions.find(function (q) {
      return q.ID === question.GROUP_QUESTION;
    }) : undefined;

    return group ? group.NAME : undefined;
  };

  // Moves the question up on the list
  $scope.moveQuestionUp = function (index) {
    swapQuestions(index - 1, index);
  };

  // Moves the question down on the list
  $scope.moveQuestionDown = function (index) {
    swapQuestions(index, index + 1);
  };

  // Opens a modal to link an existing question to form
  $scope.openLinkQuestionModal = function () {
    const modalInstance = $modal.open({
      size: 'lg',
      templateUrl: 'view/modal/linkQuestionModal.html',
      backdrop: 'static',
      controller: 'LinkQuestionModalController'
    });

    // User presses Save button
    modalInstance.result.then(function (questionID) {
      if (questionID == 'NEW_QUESTION_ADDED') {
        loadQuestions();
      } else {
        // use $scope.questions.length + 1 as the next position so the new question will be on the last position on the form
        Forms.linkQuestionToForm(formID, questionID, $scope.questions.length + 1, function (data, status) {
          if (status < 400) {
            loadQuestions();
          }
        });
      }
    });
  };

  // Opens a modal to link a new question to form
  $scope.openNewQuestionModal = function () {
    const modalInstance = $modal.open({
      size: 'lg',
      templateUrl: 'view/modal/newQuestionModal.html',
      backdrop: 'static',
      controller: 'NewQuestionModalController'
    });

    // User presses Save button
    modalInstance.result.then(function (questionID) {
      // use $scope.questions.length + 1 as the next position so the new question will be on the last position on the form
      Forms.linkQuestionToForm(formID, questionID, $scope.questions.length + 1, function (data, status) {
        if (status < 400) {
          loadQuestions();
        }
      });
    });
  };

  // Opens a modal to modify display logic for form.
  $scope.modifyQuestionDisplayLogic = function (question) {
    $rootScope.selectedLogicQuestion = question;
    const modalInstance = $modal.open({
      size: 'lg',
      templateUrl: 'view/modal/modifyQuestionDisplayLogicModal.html',
      backdrop: 'static',
      controller: 'ModifyQuestionDisplayLogicModalController'
    });

    // User presses Save button
    modalInstance.result.then(function (questionID) {
      Questions.loadDisplayRules(formID, questionID, $translate.use(), function (data, status) {
        question.DISPLAYRULES = data;
        toastr.success('N\xE4ytt\xF6logiikka lis\xE4tty kysymykselle ID: ' + questionID);
        checkForIllegalDisplayRules(question);
      });
    });
  };

  // Opens a modal to confirm question removal and removes question when user clicks ok
  $scope.removeQuestion = function (question) {
    const modalInstance = $modal.open({
      size: 'lg',
      templateUrl: 'view/modal/deleteConfirmationModal.html',
      backdrop: 'static',
      controller: 'DeleteConfirmationModalController',
      resolve: {
        confirmationMessage() {
          return $translateVar('FORM_QUESTION_REMOVE_CONFIRM_1') + ' ' + question.NAME + ' ' + $translateVar('FORM_QUESTION_REMOVE_CONFIRM_2');
        }
      }
    });

    // User presses Save button
    modalInstance.result.then(function () {
      Forms.removeLinkQuestionToForm(formID, question.ID, function (data, status) {
        loadQuestions();
        $scope.selectedQuestion = { ID: undefined };
      });
    });
  };

  $scope.saveDisplayRuleOperator = function (operator, operatorid, question) {
    const operatorinfo = {};
    operatorinfo.OPERATOR = operator;
    Questions.saveDisplayRuleOperator(operatorinfo, operatorid, function (data, status) {
      toastr.success('Nyttlogiikan operaattori tallennettu');
      checkForIllegalDisplayRules(question);
    });
  };

  // Set starting day to monday
  $scope.calendarOptions = {
    'starting-day': '1'
  };
  // Toggles the pop-up date picker for selecting start date.
  $scope.openStartDateCalendar = function ($event) {
    $event.preventDefault();
    $event.stopPropagation();
    $scope.calendar1Open = !$scope.calendar1Open;
  };

  $scope.hasInvalidLogics = function (question) {
    return (question.DISPLAYRULES || []).some(function (logic) {
      return logic.invalid;
    });
  };

  $scope.hasIllegalDisplayRules = function (question) {
    return $rootScope.questionsWithIllegalDisplayRules.some(function (q) {
      return q.ID === question.ID;
    });
  };

  // Opens a modal to confirm question display logic removal and removes logic when user clicks ok
  $scope.deleteRule = function (logicId, question) {
    const modalInstance = $modal.open({
      size: 'lg',
      templateUrl: 'view/modal/deleteConfirmationModal.html',
      backdrop: 'static',
      controller: 'DeleteConfirmationModalController',
      resolve: {
        confirmationMessage() {
          return $translateVar('DISPLAY_LOGIC_REMOVE_CONFIRM');
        }
      }
    });
    // User presses Save button
    modalInstance.result.then(function () {
      Questions.removeDisplayLogic(logicId, function (status) {
        if (status < 400) {
          question.DISPLAYRULES = question.DISPLAYRULES.filter(function (l) {
            return l.ID !== logicId;
          });
          toastr.success('Nyttlogiikka poistettu');
        }
      });
    });
  };
}]);