angular.module('HeaderCtrl', ['angularUtils.directives.dirPagination', 'ui.bootstrap']).controller('HeaderController', ['$scope', '$state', '$http', '$rootScope', '$translate', 'Organization', function ($scope, $state, $http, $rootScope, $translate, Organization) {
  $scope.$state = $state;

  if ($rootScope.loggedUser) {
    $scope.username = $rootScope.loggedUser.fullname;

    if ($rootScope.loggedUser.organizationName == null) {
      Organization.loadOrganizations(function (data, status) {
        $rootScope.organizations = data;
        for (let i = 0; data.length > i; i++) {
          if (data[i].ID == $rootScope.loggedUser.organization) {
            $rootScope.loggedUser.organizationName = data[i].NAME;
            $scope.userorganization = $rootScope.loggedUser.organizationName;
          }
        }
      });
    } else {
      $scope.userorganization = $rootScope.loggedUser.organizationName;
    }
  }

  $scope.isLoggedIn = $rootScope.loggedIn;

  $scope.getCurrentLang = function () {
    return $translate.use();
  };

  // Changes site language
  $scope.selectLang = function (toLang) {
    $translate.use(toLang);
    $rootScope.selectedLanguage = toLang;
  };

  $scope.logout = function () {
    $http.get($rootScope.api_prefix + 'logout').success(function () {
      $state.go('login');
    }).error(function () {});
  };

  $scope.authAddress = $rootScope.api_prefix + 'auth';
  $scope.logoutLink = $rootScope.logout_page;
}]);