/* Used with login-view.html to try and log user in
* from Login state
*/

angular.module('LoginCtrl', ['ui.router', 'ui.bootstrap']).controller('LoginController', ['$scope', '$http', '$state', '$stateParams', '$rootScope', '$location', '$window', function ($scope, $http, $state, $stateParams, $rootScope, $location, $window) {
  $scope.errorMessage = ''; // Message to user if login fails
  $scope.showError = false;

  $scope.login = function () {
    $scope.errorMessage = '';
    $scope.showError = false;

    const user = {
      username: $scope.username,
      password: $scope.password
    };

    $http.post($rootScope.api_prefix + 'login', user).success(function (data) {
      // Logged user in
      $rootScope.loggedIn = true;
      $rootScope.loggedUser = data;
      $state.go('secured.main');
    }).error(function (data) {
      // Show error msg since login failed
      $scope.errorMessage = 'Login failed!';
      $scope.showError = true;
      $rootScope.loggedUser = null;
    });
  };
  $scope.feedback = [];
  // Adding all querystring values as variables to the controller.
  function FromQueryStringToScope(value) {
    if ($location.search().hasOwnProperty(value)) {
      $scope.feedback[value] = $location.search()[value];
    }
  }
  FromQueryStringToScope('fail');

  // If login failed. Do this.
  if ($scope.feedback.fail) {
    $scope.showFailMessage = true;
  } else {
    $scope.showFailMessage = false;
  }

  $scope.authAddress = $rootScope.api_prefix + 'auth';
  $scope.userinfoLink = $rootScope.userinfo_page;

  $scope.openUserinfolink = function () {
    $window.open($scope.userinfoLink);
  };
}]);