angular.module('QuestionListCtrl', ['angularUtils.directives.dirPagination', 'ui.bootstrap']).controller('QuestionListController', ['$scope', '$rootScope', '$modal', 'Questions', 'Forms', '$translate', 'Organization', function ($scope, $rootScope, $modal, Questions, Forms, $translate, Organization) {
  $scope.currentPage = 1;
  $scope.questionPageSize = 50;
  $scope.displayQuestions = Questions.getDisplayStatus;
  let organizationsLoaded = false;
  // Used in sorting table contents according to selected column
  $scope.sortQuestions = {
    column: 'NAME',
    descending: false
  };
  $scope.readonlymode = true;
  if ($rootScope.loggedUser) {
    if ($rootScope.loggedUser.rightlevel == 2 || $rootScope.loggedUser.rightlevel == 3) {
      $scope.readonlymode = false;
    }
  }
  $scope.questions = Questions.getQuestions;

  $scope.questionsLoading = Questions.questionsLoading;
  Questions.loadQuestions(function (data, status) {
    Organization.loadQuestionOrganizations(function (data2, status2) {
      $scope.questionsOrganizations = data2;
      organizationsLoaded = true;
    });
  });
  // This is used in questions list-view
  $scope.printOrganizations = function (question) {
    if (organizationsLoaded == true) {
      question.ORGANIZATIONS = '';
      for (let i = 0; $scope.questionsOrganizations.length > i; i++) {
        if (question.ID == $scope.questionsOrganizations[i].QUESTION_ID) {
          if (question.ORGANIZATIONS.length > 0) {
            question.ORGANIZATIONS += ', ';
          }
          question.ORGANIZATIONS += $scope.questionsOrganizations[i].NAME;
        }
      }
      question.readonlydelete = true;
      if ($rootScope.loggedUser.rightlevel == 2) {
        if (question.ORGANIZATIONS.indexOf($rootScope.loggedUser.organizationName) != -1) {
          question.readonlydelete = false;
        }
      }
      if ($rootScope.loggedUser.rightlevel == 3) {
        question.readonlydelete = false;
      }
      return question.ORGANIZATIONS;
    }
  };

  // Switches tab from Questions to toView
  $scope.switchView = function (toView) {
    switch (toView) {
      case 'forms':
        Questions.setStatus('hide');
        Forms.setStatus('show');
        break;
    }
  };

  // Sets table sort according to COLUMN
  // Also toggles between ascending and descending
  $scope.changeSorting = function (column) {
    if ($scope.sortQuestions.column == column) {
      $scope.sortQuestions.descending = !$scope.sortQuestions.descending;
    } else {
      $scope.sortQuestions.column = column;
      $scope.sortQuestions.descending = false;
    }
  };

  // Returns icon that indicates ascending/descending sorting
  $scope.getIcon = function (column) {
    if ($scope.sortQuestions.column == column) {
      if ($scope.sortQuestions.descending) {
        return 'glyphicon-triangle-bottom';
      }
      return 'glyphicon-triangle-top';
    }
  };
}]);