// Used as a controller in previewing form

angular.module('FormPreviewCtrl', ['ui.bootstrap']).controller('FormPreviewController', ['$scope', '$q', '$stateParams', '$location', 'Forms', 'Questions', '$filter', '$translate', 'Ebmeds', '$rootScope', function ($scope, $q, $stateParams, $location, Forms, Questions, $filter, $translate, Ebmeds, $rootScope) {
  // Helper function to convert value to lowercase string.
  // Function is needed for display logic: compare freetext input display rules. Case insensitive compare.
  function lowerCaseString(value) {
    if (typeof value === 'string') {
      const returnValue = value.toLowerCase();
      return returnValue;
    }

    return value;
  }

  // do not display preview unless it is completed.
  $scope.PreviewLoaded = false;

  $scope.useLiveMode = true;
  // Set starting day to monday
  $scope.calendarOptions = {
    'starting-day': '1'
  };
  $scope.opened = [];
  // Toggles the pop-up date picker for selecting start date.
  $scope.openStartDateCalendar = function ($event, indexvalue) {
    $event.preventDefault();
    $event.stopPropagation();
    $scope.opened[indexvalue] = true;
  };

  // get form id from url.
  $scope.formID = $stateParams.formid;

  // get translations.
  $scope.restoreLanguage = $translate.use();
  $scope.previewLanguage = $rootScope.previewLanguage || {
    displayLanguage: 'FI',
    dataLanguage: 'fi-FI'
  };

  $translate.use($scope.previewLanguage.displayLanguage);
  const $translateVar = $filter('translate');

  $scope.$on('$destroy', function () {
    $translate.use($scope.restoreLanguage);
  });

  // open form details.
  $scope.detailsForm = function (formID) {
    $location.path('/form/' + $scope.formID).replace();
  };

  // load form details.
  $scope.form = {};
  Forms.loadFormByID($scope.formID, function (data, status) {
    $scope.form = data;
    if ($scope.form.INTRODUCTION) {
      $scope.form.INTRODUCTION = $scope.form.INTRODUCTION.replace(/<a href=/g, '<a target="_blank" href=');
    } else {
      $scope.form.INTRODUCTION = $translateVar('NOT_FOUND_INTRODUCTION');
    }
  }, { language: $scope.previewLanguage.dataLanguage });

  // load form question details.
  $scope.questions = [];
  Forms.loadQuestionsInForm($scope.formID, function (data, status) {
    $scope.questions = data;
    $scope.questions.forEach(function (question) {
      if (question.QUESTIONTEXT) {
        question.QUESTIONTEXT = question.QUESTIONTEXT.replace('<a href=', '<a target="_blank" href=');
      } else if (question.TYPE !== 'SUBTITLE') {
        question.QUESTIONTEXT = $translateVar('NOT_FOUND_QUESTION_TEXT');
      }
    });
    // setting up status options.
    $scope.statuses = [{ statusName: $translateVar('DRAFT'), statusValue: 'Draft' }, {
      statusName: $translateVar('VALIDATION_PENDING'),
      statusValue: 'Validation Pending'
    }, {
      statusName: $translateVar('PRODUCTION'),
      statusValue: 'Production'
    }, { statusName: $translateVar('DISCARDED'), statusValue: 'Discarded' }];
    // Fetch answers for questions
    $scope.questions.forEach(function (question) {
      if (question.TYPE == 'CHECK' || question.TYPE == 'SELECT' || question.TYPE == 'NUMBER') {
        Questions.loadAnswers(question.ID, function (data, status) {
          question.ANSWERS = data;
          question.FORMANSWER = '';
          $scope.questions.forEach(function (questionAnswer) {
            if (questionAnswer.ANSWERS) {
              questionAnswer.ANSWERS.forEach(function (answer, index) {
                if (!answer.ANSWER_TEXT) {
                  answer.ANSWER_TEXT = $translateVar('NOT_FOUND_ANSWER_TEXT');
                }
                answer.FORMANSWER = '';

                var choiceQuestion = questionAnswer.TYPE == 'CHECK' || questionAnswer.TYPE == 'SELECT';
                var hasNoneOfTheAboveChoice = questionAnswer.NONE_ANSWER == 'Y';
                var isTheLastChoice = index == questionAnswer.ANSWERS.length - 1;;
                var isMissingNoneOfTheAbove = questionAnswer.ANSWERS.filter(function (a) {
                  return a.ID === -1;
                }).length === 0;
                if (choiceQuestion && hasNoneOfTheAboveChoice && isTheLastChoice && isMissingNoneOfTheAbove) {
                  questionAnswer.ANSWERS.push({
                    ANSWER_TEXT: $translateVar('NONE_OF_THE_ABOVE'),
                    ID: -1
                  });
                }
              });
            }
          });
        }, { language: $scope.previewLanguage.dataLanguage });
      } else {
        question.ANSWERS = [];
        question.FORMANSWER = '';
      }
    });

    // Fetch display rules for questions
    // Must be 'EN' to use correct display logics
    Questions.loadDisplayRulesAll($scope.formID, 'EN', function (data, status) {
      $scope.questions.forEach(function (question) {
        question.NUMBEROFDISPLAYRULES = 0;
        question.DISPLAYRULES = [];
        for (let i = 0; data.length > i; i++) {
          if (question.ID == data[i].QUESTION_ID) {
            question.DISPLAYRULES.push(data[i]);
            question.NUMBEROFDISPLAYRULES++;
          }
        }
      });
    });
  }, { language: $scope.previewLanguage.dataLanguage });

  // Apply display rules to webform
  let containcheck = '';
  let hideit = '';
  let orShow = false;
  $scope.$watch('questions', function (newValue) {
    hideit = '';
    $scope.questions.forEach(function (question) {
      hideit = '';
      orShow = false;
      if (question.DISPLAYRULES) {
        question.DISPLAYRULES.forEach(function (rule, index) {
          if (index === 0 && question.DISPLAYRULES.length > 1) {
            rule.OPERATOR = question.DISPLAYRULES[1].OPERATOR;
          }
          $scope.questions.forEach(function (questionrule) {
            if (rule.TYPE === 'CHECK' || rule.TYPE === 'SELECT') {
              rule.VALUE = rule.ANSWER_ID;
            }
            if (questionrule.FORMANSWER == null) {
              questionrule.FORMANSWER = '';
            }
            if (parseInt(rule.ANSWER_QUESTION_ID, 10) === parseInt(questionrule.ID, 10)) {
              containcheck = '';
              if (rule.LOGIC === 'contains' || rule.TYPE === 'CHECK') {
                containcheck = false;
                if (lowerCaseString(questionrule.FORMANSWER).indexOf(lowerCaseString(rule.VALUE)) > -1) {
                  containcheck = true;
                }
              }
              if (rule.LOGIC === 'starts with') {
                containcheck = lowerCaseString(questionrule.FORMANSWER).indexOf(lowerCaseString(rule.VALUE)) === 0;
              }
              if (rule.TYPE === 'SELECT') {
                questionrule.FORMANSWER = parseInt(questionrule.FORMANSWER, 10);
              }
              if ((lowerCaseString(questionrule.FORMANSWER) === lowerCaseString(rule.VALUE) && (rule.LOGIC === 'equals' || rule.TYPE === 'YESNO' || rule.TYPE === 'SELECT') || Number(questionrule.FORMANSWER) > Number(rule.VALUE) && rule.LOGIC === 'greater than' || questionrule.FORMANSWER !== '' && Number(questionrule.FORMANSWER) < Number(rule.VALUE) && rule.LOGIC === 'less than' || containcheck === true) && questionrule.FORMHIDE !== true) {
                if (rule.OPERATOR) {
                  if (rule.OPERATOR === 'OR') {
                    orShow = true;
                    question.FORMHIDE = false;
                  }
                }
                if (question.FORMHIDE) {
                  if (hideit !== true) {
                    question.FORMHIDE = false;
                  }
                } else {
                  question.FORMHIDE = false;
                }
              } else {
                question.FORMHIDE = true;
                hideit = true;
                if (orShow === true) {
                  hideit = false;
                  question.FORMHIDE = false;
                }
              }
            }
          });
        });
      }
    });
    // do not display preview unless it is completed.
    $scope.PreviewLoaded = true;
  }, true);

  // handle clicking of checkboxes
  $scope.CheckClick = function (question, answer) {
    let noneChecked = true;
    for (let i = 0; $scope.questions.length > i; i++) {
      if ($scope.questions[i].ID == question.ID) {
        if ($scope.questions[i].FORMANSWER.indexOf(';' + answer.ID + ';') > -1) {
          $scope.questions[i].FORMANSWER = $scope.questions[i].FORMANSWER.replace(';' + answer.ID + ';', '');
          if (answer.ID == -1) {
            for (var j = 0; $scope.questions[i].ANSWERS.length > j; j++) {
              $scope.questions[i].ANSWERS[j].READONLY = false;
            }
          }
          for (var j = 0; $scope.questions[i].ANSWERS.length > j; j++) {
            if ($scope.questions[i].ANSWERS[j].FORMANSWER == true) {
              noneChecked = false;
            }
          }
          if (noneChecked == true) {
            for (var j = 0; $scope.questions[i].ANSWERS.length > j; j++) {
              if ($scope.questions[i].ANSWERS[j].ID == -1) {
                $scope.questions[i].ANSWERS[j].FORMANSWER = true;
                $scope.questions[i].FORMANSWER = ';' + $scope.questions[i].ANSWERS[j].ID + ';';
              }
            }
          }
        } else if (answer.ID == -1) {
          for (var j = 0; $scope.questions[i].ANSWERS.length > j; j++) {
            if ($scope.questions[i].ANSWERS[j].ID != -1) {
              $scope.questions[i].ANSWERS[j].FORMANSWER = false;
              $scope.questions[i].ANSWERS[j].READONLY = false;
            }
          }
          $scope.questions[i].FORMANSWER = ';' + answer.ID + ';';
        } else {
          $scope.questions[i].FORMANSWER += ';' + answer.ID + ';';
          for (var j = 0; $scope.questions[i].ANSWERS.length > j; j++) {
            if ($scope.questions[i].ANSWERS[j].ID == -1) {
              $scope.questions[i].ANSWERS[j].FORMANSWER = false;
              $scope.questions[i].FORMANSWER = $scope.questions[i].FORMANSWER.replace(';' + $scope.questions[i].ANSWERS[j].ID + ';', '');
            }
          }
        }
      }
    }
  };

  /**
   * Common units
   *
   * @see FormPreviewCtrl
   * @see NewQuestionModalController
   * @see QuestionDetailsCtrl
   * @see api/common/fhir.js
   */
  $scope.Units = [{ value: 1, text: 'kg' }, { value: 2, text: 'cm' }, { value: 3, text: 'mg' }, { value: 4, text: 'C' }, { value: 5, text: $translateVar('UNITS') }, { value: 6, text: $translateVar('YEAR') }, { value: 7, text: $translateVar('DAY') }, { value: 8, text: 'mmHg' }, { value: 9, text: 'h' }, { value: 10, text: 'mmol/l' }];

  // handle sending preview form data to ebmeds engine.
  $scope.sendtoEBMEDS = function (form, questions) {
    $scope.feedbackFromEBMeDSResponse = '';
    $scope.nofeedback = false;
    $scope.showrequest = false;
    $scope.showresponse = false;
    form.useLiveMode = $scope.useLiveMode;

    for (let i = 0; questions.length > i; i++) {
      if (questions[i].TYPE == 'SELECT') {
        for (let j = 0; questions[i].ANSWERS.length > j; j++) {
          if (questions[i].FORMANSWER == questions[i].ANSWERS[j].ID) {
            questions[i].FORMANSWER = questions[i].ANSWERS[j].ID.toString();
            if (questions[i].ANSWERS[j].ANSWER_VALUE) {
              questions[i].ANSWER_VALUE = questions[i].ANSWERS[j].ANSWER_VALUE.toString();
            } else {
              questions[i].ANSWER_VALUE = '';
            }
            j = questions[i].ANSWERS.length;
          }
        }
      }
      if (questions[i].TYPE == 'NUMBER' && questions[i].ANSWERS[0]) {
        const unit = $scope.Units.find(function (u) {
          return u.value === questions[i].ANSWERS[0].UNIT_ID;
        });
        if (unit) {
          questions[i].ANSWERS[0].UNIT_ID = unit.text;
        }
      }
    }

    const parseQuestionnaires = function (questionnnaires) {
      if (!questionnnaires) {
        return [];
      }

      return questionnnaires.reduce(function (arr, q) {
        q.recommendations.forEach(function (r) {
          arr.push({ status: r.status, text: r.texts.patient || r.texts.practitioner });
        });
        return arr;
      }, []);
    };

    $scope.formatRecommendation = function (recommendation) {
      return recommendation.text + ' (Status: ' + recommendation.status + ')';
    };

    form.language = $scope.previewLanguage.dataLanguage;
    form.questions = questions;
    Ebmeds.callEBMeDS(form, function (data) {
      $scope.viewFeedback = parseQuestionnaires(data.responseBody.questionnaire);
      $scope.nofeedback = $scope.viewFeedback.length === 0;
      $scope.feedbackFromEBMeDSResponse = JSON.stringify(data.responseBody, undefined, 2);
      $scope.feedbackFromEBMeDSRequest = JSON.stringify(data.requestBody, undefined, 2);
      $scope.showresponse = true;
      $scope.showrequest = true;

      finalize();
    });

    const finalize = function () {
      for (let i = 0; questions.length > i; i++) {
        if (questions[i].TYPE == 'SELECT') {
          for (let j = 0; questions[i].ANSWERS.length > j; j++) {
            if (questions[i].FORMANSWER == questions[i].ANSWERS[j].ID) {
              questions[i].FORMANSWER = questions[i].ANSWERS[j].ID;
              questions[i].ANSWER_VALUE = questions[i].ANSWERS[j].ANSWER_VALUE;
              j = questions[i].ANSWERS.length;
            }
          }
        }
      }
      document.getElementById('focusButton').scrollIntoView();
      $('html, body').animate({ scrollTop: $('#focusButton').offset().top }, 500);
    };
  };
}]);