/* Generic modal for confirming deletion
*/
angular.module('DeleteConfirmationModalCtrl', ['ui.bootstrap']).controller('DeleteConfirmationModalController', ['$scope', '$modalInstance', 'confirmationMessage', function ($scope, $modalInstance, confirmationMessage) {
  $scope.confirmationMessage = confirmationMessage;

  $scope.ok = function () {
    $modalInstance.close();
  };

  $scope.cancel = function () {
    $modalInstance.dismiss('cancel');
  };
}]);