// Controller for the modal that is used to link an existing question to form

angular.module('NewQuestionModalCtrl', ['angularUtils.directives.dirPagination', 'ui.bootstrap']).controller('NewQuestionModalController', ['$scope', '$rootScope', '$modalInstance', 'Questions', '$state', '$stateParams', '$timeout', '$filter', '$translate', '$window', function ($scope, $rootScope, $modalInstance, Questions, $state, $stateParams, $timeout, $filter, $translate, $window) {
  $scope.questionInfo = {
    CODES: [],
    TYPE: ''
  };
  const $translateVar = $filter('translate');
  $scope.detailsPageTitle = $translateVar('NEW_QUESTION');
  // Set starting day to monday
  $scope.calendarOptions = {
    'starting-day': '1'
  };
  // Toggles the pop-up date picker for selecting start date.
  $scope.openStartDateCalendar = function ($event) {
    $event.preventDefault();
    $event.stopPropagation();
    $scope.calendar1Open = !$scope.calendar1Open;
  };
  // Array of question types, used with radio buttons on form
  if ($translate.use() === 'FI') {
    $scope.questionTypes = Questions.getQuestionTypes;
  }
  if ($translate.use() === 'EN') {
    $scope.questionTypes = Questions.getQuestionTypesEN;
  }
  // Tooltip texts
  $scope.tooltipMoveUp = $translateVar('MOVE_QUESTION_UP');
  $scope.tooltipMoveDown = $translateVar('MOVE_QUESTION_DOWN');
  $scope.disabledID = true;
  $scope.disabled = false;
  $scope.disabledApproval = true;

  let newID = '';

  /**
   * Common units
   *
   * @see FormPreviewCtrl
   * @see NewQuestionModalController
   * @see QuestionDetailsCtrl
   * @see api/common/fhir.js
   */
  $scope.Units = [{ value: 1, text: 'kg' }, { value: 2, text: 'cm' }, { value: 3, text: 'mg' }, { value: 4, text: 'C' }, { value: 5, text: $translateVar('UNITS') }, { value: 6, text: $translateVar('YEAR') }, { value: 7, text: $translateVar('DAY') }, { value: 8, text: 'mmHg' }, { value: 9, text: 'h' }, { value: 10, text: 'mmol/l' }];

  $scope.codeSystems = [];
  $scope.systemCodes = {};
  Questions.loadCodeSystems(function (data) {
    $scope.codeSystems = data.items;

    data.items.forEach(function (system) {
      Questions.loadSystemCodes(system.ID, function (data) {
        $scope.systemCodes[system.ID] = data;
      });
    });
  });

  // Saves new question in DB, then redirects to the question details page
  $scope.saveQuestion = function () {
    $scope.questionInfo.STATUS = 'Draft';
    $scope.questionInfo.ORGANIZATIONS = [{ ORG_ID: $rootScope.loggedUser.organization }];
    if ($scope.questionForm.$invalid) {
      return;
    }

    Questions.createQuestion($scope.questionInfo, function (status, data) {
      if (status < 400) {
        newID = data.NEW_ID;
        $scope.questionInfo.ORGANIZATIONS = [{ ORG_ID: $rootScope.loggedUser.organization, QUESTION_ID: newID }];
        const timeout = null;
        $scope.isLoading = Questions.questionsLoading;
        $scope.questionID = newID;
        $scope.answers = [];
        $scope.oldQuestionInfo = {};
        // Array for question answer choices
        $scope.placeholderText = $translateVar('TYPE_ANSWER_TEXT');
        $scope.detailsPageTitle = $translateVar('QUESTION_TITLE_1') + ' "' + $scope.questionID + '" ' + $translateVar('QUESTION_TITLE_2');
        // Array of question types, used with radio buttons on form
        if ($translate.use() === 'FI') {
          $scope.questionTypes = Questions.getQuestionTypes;
        }
        if ($translate.use() === 'EN') {
          $scope.questionTypes = Questions.getQuestionTypesEN;
        }

        // Tooltip texts
        $scope.tooltipMoveUp = $translateVar('MOVE_QUESTION_UP');
        $scope.tooltipMoveDown = $translateVar('MOVE_QUESTION_DOWN');

        // Initialize question and answer information
        Questions.loadQuestion($scope.questionID, function (status) {
          if (status < 400) {
            $scope.questionInfo = Questions.getQuestion;
            $scope.oldQuestionInfo = angular.copy($scope.questionInfo);

            Questions.loadAnswers($scope.questionID, function (data, status) {
              if (status < 400) {
                $scope.answers = Questions.getAnswers;
              }
            });
          }
        });
        $scope.saveQuestion = function () {
          $scope.questionInfo.ID = $scope.questionID;
          $scope.questionInfo.ORGANIZATIONS = [{ ORG_ID: $rootScope.loggedUser.organization, QUESTION_ID: $scope.questionInfo.ID }];

          // Save the question in DB
          Questions.updateQuestion($scope.questionInfo, $scope.answers, function (status) {
            if (status < 400) {
              // Delete old answer settings if type changed
              if ($scope.questionInfo.TYPE !== $scope.oldQuestionInfo.TYPE) {
                $scope.answers.forEach(function (value, index, array) {
                  Questions.deleteAnswer($scope.questionID, value.ID);
                });
                Questions.loadAnswers($scope.questionID, function (data, status) {
                  if (status < 400) {
                    $scope.answers = Questions.getAnswers;
                  }
                });
              } else {
                $scope.saveNumberAnswer();
                $scope.saveAnswers();
              }
              // Reset page
              $scope.oldQuestionInfo = angular.copy($scope.questionInfo);
            }
          });
        };

        $scope.addQuestionCode = function () {
          $scope.questionInfo.CODES.push({});
        };

        $scope.removeQuestionCode = function (id, codeKey) {
          if (!id) {
            // The item has not been saved yet, just remove it.
            $scope.questionInfo.CODES = $scope.questionInfo.CODES.filter(function (_, k) {
              return k !== codeKey;
            });
            return;
          }

          if ($window.confirm('Remove the code from this question?')) {
            Questions.deleteQuestionCode(id, function () {
              $scope.questionInfo.CODES = $scope.questionInfo.CODES.filter(function (c) {
                return c.ID !== id;
              });
            });
          }
        };

        $scope.addAnswerCode = function (answerId) {
          const answer = $scope.answers.find(function (a) {
            return a.ID === answerId;
          });
          answer.CODES.push({});
        };

        $scope.removeAnswerCode = function (answerId, id, codeKey) {
          const answer = $scope.answers.find(function (a) {
            return a.ID === answerId;
          });

          if (!id) {
            // The item has not been saved yet, just remove it.
            answer.CODES = answer.CODES.filter(function (_, k) {
              return k !== codeKey;
            });
            return;
          }

          if ($window.confirm('Remove the code from this answer?')) {
            Questions.deleteAnswerCode(id, function () {
              answer.CODES = answer.CODES.filter(function (c) {
                return c.ID !== id;
              });
            });
          }
        };

        // Only called from saveQuestion now.
        $scope.saveNumberAnswer = function () {
          if ($scope.questionInfo.TYPE === 'NUMBER') {
            const newAnswer = {
              ANSWER_TEXT: '',
              QUESTION_ID: $scope.questionID,
              ORDER_NUM: '',
              MIN: $scope.answers[0].MIN,
              MAX: $scope.answers[0].MAX,
              UNIT_ID: $scope.answers[0].UNIT_ID
            };

            Questions.createAnswer($scope.questionID, newAnswer, function (status) {
              if (status >= 400) {
                // Error
                Questions.loadAnswers($scope.questionID, function (data, status) {
                  if (status < 400) {
                    $scope.answers = Questions.getAnswers;
                  }
                });
              }
            });
          }
        };
        // Adds new choice input at selected index
        $scope.addAnswer = function (text) {
          if ($scope.answers.length < 10) {
            const newAnswer = {
              ANSWER_TEXT: text,
              QUESTION_ID: $scope.questionID,
              ORDER_NUM: $scope.answers.length,
              MIN: '',
              MAX: '',
              UNIT_ID: ''
            };
            // Save the answer in database
            Questions.createAnswer($scope.questionID, newAnswer, function (status) {
              if (status < 400) {
                Questions.loadAnswers($scope.questionID, function (data, status) {
                  if (status < 400) {
                    $scope.answers = Questions.getAnswers;
                  }
                });
              }
            });
          }
        };

        // Removes selected answer choice input from array
        $scope.removeAnswer = function (answerID) {
          // Delete the answer choice from database
          Questions.deleteAnswer($scope.questionID, answerID, function (status) {
            if (status < 400) {
              // Reload the answer list
              Questions.loadAnswers($scope.questionID, function (data, status) {
                if (status < 400) {
                  $scope.answers = Questions.getAnswers;
                }
              });
            }
          });
        };

        // Moves the answer choice up on the list
        // unless it's the first one
        $scope.moveUp = function (answerID) {
          // Get answer position
          let index = 0;
          for (let i = 0; i < $scope.answers.length; i++) {
            if (answerID == $scope.answers[i].ID) {
              index = i;
              break;
            }
          }

          if (index !== 0) {
            // Update the position in DB
            Questions.switchAnswers(index, index - 1, function (status) {
              if (status >= 400) {
                // Reload if error
                Questions.loadAnswers($scope.questionID, function (data, status) {
                  $scope.answers = Questions.getAnswers;
                });
              }
            });
          }
        };

        // Moves the answer choice down on the list
        // unless it's the last one
        $scope.moveDown = function (answerID) {
          // Get answer position
          let index = $scope.answers.length - 1;
          for (let i = 0; i < $scope.answers.length; i++) {
            if (answerID == $scope.answers[i].ID) {
              index = i;
              break;
            }
          }

          if (index !== $scope.answers.length - 1) {
            // Update the position in DB
            Questions.switchAnswers(index, index + 1, function (status) {
              if (status >= 400) {
                // Reload if error
                Questions.loadAnswers($scope.questionID, function (data, status) {
                  $scope.answers = Questions.getAnswers;
                });
              }
            });
          }
        };

        // Only called from saveQuestion now.
        $scope.saveAnswers = function () {
          $scope.answers.forEach(function (answer) {
            Questions.editAnswer(answer, function (data, status) {
              if (status < 400) {
                $scope.isLoading.answersSuccess = true;
                $timeout(function () {
                  $scope.isLoading.answersSuccess = false;
                }, 2000);
              }
            });
          });
        };
      }
    });
  };

  // Array of question types, used with radio buttons on form
  if ($translate.use() === 'FI') {
    $scope.questionTypeOptions = Questions.getQuestionTypes;
  }
  if ($translate.use() === 'EN') {
    $scope.questionTypeOptions = Questions.getQuestionTypesEN;
  }

  $scope.ok = function () {
    if ($scope.questionID) {
      $modalInstance.close(newID);
    }
  };
  $scope.cancel = function () {
    $modalInstance.dismiss('cancel');
  };
}]);