angular.module('FormService', []).factory('Forms', ['$http', '$rootScope', '$filter', '$translate', 'toastr', function ($http, $rootScope, $filter, $translate, toastr) {
  const api_prefix = $rootScope.api_prefix;

  const forms = [];

  const loading = { forms: false, form: false };
  const displayForms = { value: true };

  // Extracts the language option from an options object
  function getLanguage(options) {
    return options && options.language || 'fi-FI';
  }

  // Load all forms from the database
  function loadForms(callback) {
    loading.forms = true;
    $http.get(api_prefix + 'api/forms').success(function (data, status) {
      // Angular copy so that the change is noticed in Controller and 2-way binding works
      angular.copy(data, forms);

      if (callback) {
        callback(status);
      }
    }).error(function (data, status) {
      // Show error message to user
      toastr.error('Fetching data failed with status: ' + status);

      if (callback) {
        callback(status);
      }
    }).finally(function () {
      loading.forms = false;
    });
  }

  // Load a single form from the database
  function loadFormByID(formID, callback, options) {
    loading.form = true;
    $http.get(api_prefix + 'api/forms/' + formID + '?lang=' + getLanguage(options)).success(function (data, status) {
      if (callback) {
        callback(data, status);
      }
    }).error(function (data, status) {
      // Show error message to user
      toastr.error('Fetching data failed with status: ' + status);

      if (callback) {
        callback(data, status);
      }
    }).finally(function () {
      loading.form = false;
    });
  }
  // Load a single form from the database
  function loadFocalUrl(formID, callback) {
    loading.form = true;
    $http.get(api_prefix + 'api/forms/link/' + formID).success(function (data, status) {
      if (callback) {
        callback(data.url, status);
      }
    }).error(function (data, status) {
      // Show error message to user
      toastr.error('Fetching data failed with status: ' + status);

      if (callback) {
        callback(data, status);
      }
    }).finally(function () {
      loading.form = false;
    });
  }

  // Creates a new form and saves it in database
  function createForm(form, callback) {
    loading.form = true;

    form.CREATED_BY = $rootScope.loggedUser.id;

    const $translateVar = $filter('translate');

    if (form.INTRODUCTION && form.INTRODUCTION.length > 4000) {
      toastr.error($translateVar('INTRODUCTION_LENGTH'));
      return;
    }
    if (form.DESCRIPTION && form.DESCRIPTION.length > 4000) {
      toastr.error($translateVar('DESCRIPTION_LENGTH'));
      return;
    }

    $http.post(api_prefix + 'api/forms/', form).success(function (data, status) {
      toastr.success('New form created successfully');

      if (callback) {
        callback(data, status);
      }
    }).error(function (data, status) {
      // Show error message to user
      toastr.error('Creating new form failed with status: ' + status);

      if (callback) {
        callback(data, status);
      }
    }).finally(function () {
      loading.form = false;
    });
  }

  // Saves an edited form to database
  function editForm(form, callback) {
    loading.form = true;
    // Initialize undefined valuesto empty strings so database wont be filled with 'undefined'
    form.NAME = form.NAME ? form.NAME : '';
    form.DESCRIPTION = form.DESCRIPTION ? form.DESCRIPTION : '';
    form.STATUS = form.STATUS ? form.STATUS : '';
    form.APPROVED_BY = form.APPROVED_BY ? form.APPROVED_BY : '';
    form.COPYRIGHT = form.COPYRIGHT ? form.COPYRIGHT : '';
    form.ORGANISATION = form.ORGANISATION ? form.ORGANISATION : '';
    form.VALIDATED = form.VALIDATED ? form.VALIDATED : '';

    form.EDITED_BY = $rootScope.loggedUser.id;

    const $translateVar = $filter('translate');
    if (form.INTRODUCTION && form.INTRODUCTION.length > 4000) {
      toastr.error($translateVar('INTRODUCTION_LENGTH'));
      return;
    }
    if (form.DESCRIPTION && form.DESCRIPTION.length > 4000) {
      toastr.error($translateVar('DESCRIPTION_LENGTH'));
      return;
    }

    $http.put(api_prefix + 'api/forms/' + form.ID, form).success(function (data, status) {
      toastr.success('Form edited successfully');

      if (callback) {
        callback(data, status);
      }
    }).error(function (data, status) {
      // Show error message to user
      toastr.error('Editing form failed with status: ' + status);

      if (callback) {
        callback(data, status);
      }
    }).finally(function () {
      loading.form = false;
    });
  }

  // Adds a question to form by inserting the ID:s in link table in database
  // Parameters: position is the numeric position of question in form, first position is 1
  function linkQuestionToForm(formID, questionID, position, callback) {
    loading.linkQuestion = true;

    $http.post(api_prefix + 'api/forms/' + formID + '/questions/' + questionID, {
      POSITION: position
    }).success(function (data, status) {
      toastr.success('Question ' + questionID + ' added to form');

      if (callback) {
        callback(data, status);
      }
    }).error(function (data, status) {
      toastr.error('Adding question to form failed with status: ' + status);

      if (callback) {
        callback(data, status);
      }
    }).finally(function () {
      loading.linkQuestion = false;
    });
  }

  // Loads all the questions in the form ordered by their position in the form. Positions start from 1
  function loadQuestionsInForm(formID, callback, options) {
    loading.formQuestions = true;
    $http.get(api_prefix + 'api/forms/' + formID + '/questions?lang=' + getLanguage(options)).success(function (data, status) {
      if (callback) {
        callback(data, status);
      }
    }).error(function (data, status) {
      // Show error message to user
      toastr.error('Fetching data failed with status: ' + status);

      if (callback) {
        callback(data, status);
      }
    }).finally(function () {
      loading.formQuestions = false;
    });
  }

  // Removes a question from a form
  function removeLinkQuestionToForm(formID, questionID, callback) {
    loading.linkQuestion = true;

    $http.delete(api_prefix + 'api/forms/' + formID + '/questions/' + questionID).success(function (data, status) {
      toastr.success('Question ' + questionID + ' removed from form');

      if (callback) {
        callback(data, status);
      }
    }).error(function (data, status) {
      toastr.error('Removing question from form failed with status: ' + status);

      if (callback) {
        callback(data, status);
      }
    }).finally(function () {
      loading.linkQuestion = false;
    });
  }

  // Loads all the notes in form by form id
  function loadNotesInForm(formID, callback) {
    loading.notes = true;
    $http.get(api_prefix + 'api/forms/' + formID + '/notes').success(function (data, status) {
      if (callback) {
        callback(data, status);
      }
    }).error(function (data, status) {
      // Show error message to user
      toastr.error('Fetching data failed with status: ' + status);

      if (callback) {
        callback(data, status);
      }
    }).finally(function () {
      loading.notes = false;
    });
  }

  // Delete form by setting its deleted value to 1
  function deleteForm(formID, callback) {
    loading.forms = true;
    $http.delete(api_prefix + 'api/forms/' + formID).success(function (data, status) {
      toastr.success('Form ' + formID + ' deleted');

      if (callback) {
        callback(data, status);
      }
    }).error(function (data, status) {
      // Show error message to user
      toastr.error('Deleting form failed with status: ' + status);

      if (callback) {
        callback(data, status);
      }
    }).finally(function () {
      loading.forms = false;
    });
  }

  // Swaps positions of two questions in the form
  // This is intended to be used to swap the positions of two neighboring questions
  // Otherwise the positions won't be correct
  function swapQuestionPositionsInForm(formid, questionid1, questionid2, callback) {
    loading.formQuestions = true;
    $http.put(api_prefix + 'api/forms/' + formid + '/questions/swap/' + questionid1 + '/' + questionid2).success(function (data, status) {
      if (callback) {
        callback(data, status);
      }
    }).error(function (data, status) {
      // Show error message to user
      toastr.error('Question position swapping failed with status: ' + status);

      if (callback) {
        callback(data, status);
      }
    }).finally(function () {
      loading.formQuestions = false;
    });
  }

  function setQuestionGroup(formId, questionId, groupQuestion, callback) {
    $http.put(api_prefix + 'api/forms/' + formId + '/questions/' + questionId + '/group', { groupQuestion }).success(function (data, status) {
      toastr.success('Question group changed successfully');
      callback(data, status);
    }).error(function (data, status) {
      toastr.error('Saving question group failed with status: ' + status);
      callback(data, status);
    });
  }

  // Set SEND_TO_EBMEDS information on question linked to form. This field tells form system whether to send
  // Data to ebmeds or not when this particular question is answered.
  function setSendToEBMeDS(formID, questionID, sendToEBMeDS, callback) {
    $http.put(api_prefix + 'api/formsendtoebmeds/' + formID, {
      SEND_TO_EBMEDS: sendToEBMeDS,
      QUESTION_ID: questionID
    }).success(function (data, status) {
      if (callback) {
        callback(data, status);
      }
    }).error(function (data, status) {
      toastr.error('Saving setting \'Send to EBMEDS\' failed with status: ' + status);
      if (callback) {
        callback(data, status);
      }
    }).finally(function () {});
  }

  // Set options (OPTIONAL, READ_ONLY) for a question linked to form.
  function setLinkedQuestionOptions(question) {
    $http.put(api_prefix + 'api/formlinkedquestionoptions', question).success(function (data, status) {
      toastr.success('Question options changed successfully');
    }).error(function (data, status) {
      toastr.error('Saving form question options failed with status: ' + status);
    });
  }

  // Publish form to export API, save new version
  function PublishSaveFormVersion(formID, scriptID, params, callback) {
    $http.post(api_prefix + 'api/formversion/' + formID, {
      CREATED_BY: $rootScope.loggedUser.id,
      SCRIPT_ID: scriptID,
      VERSION_COMMENT: params.comment,
      languages: params.languages,
      status: params.status
    }).success(function (data, status) {
      toastr.success('Form published, new version saved');

      if (callback) {
        callback(data, status);
      }
    }).error(function (data, status) {
      toastr.error(data.error);
      if (callback) {
        callback(data, status);
      }
    }).finally(function () {});
  }

  // Loads all publish comments by a given form id
  const loadPublishCommentByFormId = function (formId, callback) {
    loading.comments = true;
    $http.get(api_prefix + 'api/form/' + formId + '/form-versions').success(function (data, status) {
      if (callback) {
        callback(data, status);
      }
    }).error(function (data, status) {
      toastr.error('Fetching data failed with status: ' + status);
    }).finally(function () {
      return loading.comments = false;
    });
  };

  const loadCurrentSavedVersionByFormId = function (formId, callback, options) {
    $http.get(api_prefix + 'api/FHIR/forms/export/' + formId + '?lang=' + getLanguage(options)).success(function (data, status) {
      if (callback) {
        callback(data, status);
      }
    }).error(function (data, status) {
      if (status === 404) {
        toastr.warning(data.errorText);
      } else {
        toastr.error('Fetching data failed with status: ' + status);
      }
    });
  };

  // Preview form to export API, save new version
  function PreviewFormVersion(formID, scriptID, callback, options) {
    $http.post(api_prefix + 'api/formodapreview/' + formID + '?lang=' + getLanguage(options), {
      CREATED_BY: $rootScope.loggedUser.id,
      SCRIPT_ID: scriptID
    }).success(function (data, status) {
      toastr.success('Form data sent to ODA2 for preview');

      if (callback) {
        callback(data, status);
      }
    }).error(function (data, status) {
      let toastrError = '';
      if (data.odaerror) {
        for (let i = 0; data.odaerror.length > i; i++) {
          if (data.odaerror[i].location) {
            toastrError += data.odaerror[i].location[0] + ':\n' + data.odaerror[i].diagnostics + '\n\n';
          } else {
            toastrError += 'Warning(s) / Errors(s):\n' + data.odaerror[i].diagnostics + '\n\n';
          }
        }
        toastr.error('ODA2 warning/error: ' + toastrError);
      } else {
        toastr.error('Form preview failed with status: ' + status);
      }

      if (callback) {
        callback(data, status);
      }
    }).finally(function () {});
  }

  // Validate form to export API, save new version
  function ValidateFormVersion(formID, scriptID, callback, options) {
    $http.post(api_prefix + 'api/formodavalidate/' + formID + '?lang=' + getLanguage(options), {
      CREATED_BY: $rootScope.loggedUser.id,
      SCRIPT_ID: scriptID
    }).success(function (data, status) {

      toastr.success('Form validated for ODA2');

      if (callback) {
        callback(data, status);
      }
    }).error(function (data, status) {
      let toastrError = '';
      if (data.odaerror) {
        for (let i = 0; data.odaerror.length > i; i++) {
          if (data.odaerror[i].location) {
            toastrError += data.odaerror[i].location[0] + ':\n' + data.odaerror[i].diagnostics + '\n\n';
          } else {
            toastrError += 'Warning(s) / Errors(s):\n' + data.odaerror[i].diagnostics + '\n\n';
          }
        }
        toastr.error('Validation warning/error: ' + toastrError);
      } else {
        toastr.error('Form validation failed with status: ' + status);
      }

      if (callback) {
        callback(data, status);
      }
    }).finally(function () {});
  }

  function setStatus(status) {
    // Sets Forms view hidden/displayed
    if (status == 'show') {
      displayForms.value = true;
    } else {
      displayForms.value = false;
    }
  }

  const hasChanged = function (form, oldForm) {
    const isEmpty = function (value) {
      return value === undefined || value === null || value === '' || value === '<br>';
    };

    const bothEmpty = function (val, val2) {
      return isEmpty(val) && isEmpty(val2);
    };

    const keys = Object.keys(form).filter(function (f) {
      return f !== 'ORGANIZATIONS';
    });

    for (const key of keys) {
      if (form[key] !== oldForm[key] && !bothEmpty(form[key], oldForm[key])) {
        return true;
      }
    }

    return !angular.equals(form.ORGANIZATIONS, oldForm.ORGANIZATIONS);
  };

  const deprecateFormVersion = function (deprecate) {
    return function (id, callback) {
      const action = deprecate ? 'deprecate' : 'reactivate';
      const url = api_prefix + 'api/formversion/' + id + '/' + action;
      $http.post(url).success(function (data, status) {
        toastr.success('Form version ' + id + ' ' + action + ' successful');
        callback(data, status);
      }).error(function (data, status) {
        toastr.error(status + ': ' + data.errormessage);
        callback(data, status);
      });
    };
  };

  return {
    getForms: forms,
    setStatus,
    getDisplayStatus: displayForms,
    loading,
    loadForms,
    loadFormByID,
    loadQuestionsInForm,
    linkQuestionToForm,
    loadNotesInForm,
    createForm,
    editForm,
    removeLinkQuestionToForm,
    deleteForm,
    swapQuestionPositionsInForm,
    setQuestionGroup,
    setSendToEBMeDS,
    setLinkedQuestionOptions,
    hasChanged,
    PublishSaveFormVersion,
    PreviewFormVersion,
    ValidateFormVersion,
    loadPublishCommentByFormId,
    loadCurrentSavedVersionByFormId,
    deprecateFormVersion,
    loadFocalUrl
  };
}]);