angular.module('FormListCtrl', ['angularUtils.directives.dirPagination', 'ui.bootstrap']).controller('FormListController', ['$scope', '$http', '$rootScope', '$modal', 'Forms', 'Questions', '$filter', '$translate', 'Organization', function ($scope, $http, $rootScope, $modal, Forms, Questions, $filter, $translate, Organization) {
  $scope.currentPage = 1;
  $scope.formPageSize = 50;
  $scope.displayForms = Forms.getDisplayStatus;
  let organizationsLoaded = false;

  // Used in sorting table contents according to selected column
  $scope.sortForms = {
    column: 'NAME',
    descending: false
  };
  $scope.readonlymode = true;
  if ($rootScope.loggedUser) {
    if ($rootScope.loggedUser.rightlevel == 2 || $rootScope.loggedUser.rightlevel == 3) {
      $scope.readonlymode = false;
    }
  }
  $scope.forms = Forms.getForms;

  $scope.loading = Forms.loading;

  Forms.loadForms(function (data, status) {
    Organization.loadFormOrganizations(function (data2, status2) {
      $scope.formsOrganizations = data2;
      organizationsLoaded = true;
    });
  });

  // This is used in forms list-view
  $scope.printOrganizations = function (form) {
    if (organizationsLoaded == true) {
      form.ORGANIZATIONS = '';
      for (let i = 0; $scope.formsOrganizations.length > i; i++) {
        if (form.ID == $scope.formsOrganizations[i].FORM_ID) {
          if (form.ORGANIZATIONS.length > 0) {
            form.ORGANIZATIONS += ', ';
          }
          form.ORGANIZATIONS += $scope.formsOrganizations[i].NAME;
        }
      }
      form.readonlydelete = true;
      if ($rootScope.loggedUser.rightlevel == 2) {
        if (form.ORGANIZATIONS.indexOf($rootScope.loggedUser.organizationName) != -1) {
          form.readonlydelete = false;
        }
      }
      if ($rootScope.loggedUser.rightlevel == 3) {
        form.readonlydelete = false;
      }
      return form.ORGANIZATIONS;
    }
  };

  const $translateVar = $filter('translate');

  // Changes tab from Forms to toView
  $scope.switchView = function (toView) {
    switch (toView) {
      case 'questions':
        Forms.setStatus('hide');
        Questions.setStatus('show');
        break;
    }
  };

  // Sets table sort according to COLUMN
  // Also toggles between ascending and descending
  $scope.changeSorting = function (column) {
    if ($scope.sortForms.column == column) {
      $scope.sortForms.descending = !$scope.sortForms.descending;
    } else {
      $scope.sortForms.column = column;
      $scope.sortForms.descending = false;
    }
  };

  // Returns icon that indicates ascending/descending sorting
  $scope.getIcon = function (column) {
    if ($scope.sortForms.column == column) {
      if ($scope.sortForms.descending) {
        return 'glyphicon-triangle-bottom';
      }
      return 'glyphicon-triangle-top';
    }
  };

  // Deletes form by setting its deleted value to 1
  $scope.deleteForm = function (formID) {
    const form = $scope.forms.find(function (f) {
      return f.ID === formID;
    });

    const modalInstance = $modal.open({
      size: 'lg',
      templateUrl: 'view/modal/deleteConfirmationModal.html',
      backdrop: 'static',
      controller: 'DeleteConfirmationModalController',
      resolve: {
        confirmationMessage() {
          return $translateVar('FORM_DELETE_CONFIRM') + ' "' + form.NAME + '"?';
        }
      }
    });

    // User presses Save button
    modalInstance.result.then(function () {
      Forms.deleteForm(formID, function (data, status) {
        Forms.loadForms();
      });
    });
  };
}]);