// Service for handling organizations

angular.module('OrganizationService', []).factory('Organization', ['$http', 'toastr', '$rootScope', function ($http, toastr, $rootScope) {
  const api_prefix = $rootScope.api_prefix;

  function loadOrganizations(callback) {
    $http.get(api_prefix + 'api/organizations').success(function (data, status) {
      if (callback) {
        callback(data, status);
      }
    }).error(function (data, status) {
      toastr.error('Loading organizations failed with status: ' + status);
      if (callback) {
        callback(data, status);
      }
    }).finally(function (data, status) {});
  }

  function loadFormOrganizations(callback) {
    $http.get(api_prefix + 'api/organizations/form').success(function (data, status) {
      if (callback) {
        callback(data, status);
      }
    }).error(function (data, status) {
      toastr.error('Loading form organizations failed with status: ' + status);
      if (callback) {
        callback(data, status);
      }
    }).finally(function (data, status) {});
  }

  function loadQuestionOrganizations(callback) {
    $http.get(api_prefix + 'api/organizations/question').success(function (data, status) {
      if (callback) {
        callback(data, status);
      }
    }).error(function (data, status) {
      toastr.error('Loading question organizations failed with status: ' + status);
      if (callback) {
        callback(data, status);
      }
    }).finally(function (data, status) {});
  }

  return {
    loadOrganizations,
    loadFormOrganizations,
    loadQuestionOrganizations
  };
}]);