angular.module('AppRoutes', ['ui.router']).config(['$stateProvider', '$urlRouterProvider', '$locationProvider', function ($stateProvider, $urlRouterProvider, $locationProvider) {
  $urlRouterProvider.otherwise('/');
  // Use html5mode so the #-character is not shown in browser url
  $locationProvider.html5Mode(true);

  $stateProvider.state('login', {
    // Login page
    url: '/login',
    views: {
      '': { templateUrl: 'view/login-main-view.html' },

      'header@login': {
        templateUrl: 'view/header-view.html',
        controller: 'HeaderController'
      },

      'login@login': {
        templateUrl: 'view/login-view.html',
        controller: 'LoginController'
      }
    }
  })
  // secured is an abstract state that encapsulates all states that require authentication
  // this state resolves the user authentication before a child state is shown
  .state('secured', {
    abstract: true,
    // This template is important because the child states views
    // are put in this ui-view after authentication is resolved
    template: '<div ui-view></div>',
    resolve: {
      auth: function ($http, $state, $rootScope, $location) {
        return $http.get($rootScope.api_prefix + 'checksession?url=' + $location.url()).then(function (res) {
          // User found
          $rootScope.loggedUser = res.data;
          $rootScope.loggedIn = true;
        }, function (res) {
          // No user logged in, redirect to login page
          $rootScope.loggedUser = null;
          $rootScope.loggedIn = false;
          $state.go('login');
        });
      }
    }
  }).state('secured.main', {
    url: '/',
    views: {
      '': { templateUrl: 'view/mainpage-view.html' },

      'header@secured.main': {
        templateUrl: 'view/header-view.html',
        controller: 'HeaderController'
      },

      'forms@secured.main': {
        templateUrl: 'view/form-list-view.html',
        controller: 'FormListController'
      },

      'questions@secured.main': {
        templateUrl: 'view/question-list-view.html',
        controller: 'QuestionListController'
      }
    }
  }).state('secured.questionDetails', {
    url: '/question/:questionid',
    views: {
      '': { templateUrl: 'view/question-details-page-view.html' },

      'header@secured.questionDetails': {
        templateUrl: 'view/header-view.html',
        controller: 'HeaderController'
      },

      'question@secured.questionDetails': {
        templateUrl: 'view/question-details-view.html',
        controller: 'QuestionDetailsController'
      },

      'questionNotes@secured.questionDetails': {
        templateUrl: 'view/note-feed-view.html',
        controller: 'NoteFeedController'
      }
    }
  }).state('secured.formDetailsPage', {
    url: '/form/:formid',
    views: {
      '': { templateUrl: 'view/form-details-page-view.html' },

      'header@secured.formDetailsPage': {
        templateUrl: 'view/header-view.html',
        controller: 'HeaderController'
      },

      'formDetails@secured.formDetailsPage': {
        templateUrl: 'view/form-details-view.html',
        controller: 'FormDetailsController'
      },

      'formQuestions@secured.formDetailsPage': {
        templateUrl: 'view/form-questions-view.html',
        controller: 'FormQuestionsController'
      },

      'formNotes@secured.formDetailsPage': {
        templateUrl: 'view/note-feed-view.html',
        controller: 'NoteFeedController'
      }
    }
  }).state('secured.formPreview', {
    url: '/formpreview/:formid',
    views: {
      '': {
        templateUrl: 'view/form-preview-view.html',
        controller: 'FormPreviewController'
      }
    }
  }).state('secured.newFormPage', {
    url: '/newform',
    views: {
      '': { templateUrl: 'view/form-details-page-view.html' },

      'header@secured.newFormPage': {
        templateUrl: 'view/header-view.html',
        controller: 'HeaderController'
      },

      'formDetails@secured.newFormPage': {
        templateUrl: 'view/new-form-view.html',
        controller: 'NewFormController'
      }
    }
  }).state('secured.newQuestionPage', {
    url: '/newquestion',
    views: {
      '': { templateUrl: 'view/question-details-page-view.html' },

      'header@secured.newQuestionPage': {
        templateUrl: 'view/header-view.html',
        controller: 'HeaderController'
      },

      'question@secured.newQuestionPage': {
        templateUrl: 'view/new-question-view.html',
        controller: 'NewQuestionController'
      }
    }
  });
}]);