angular.module('NewFormCtrl', ['angularUtils.directives.dirPagination', 'ui.bootstrap']).controller('NewFormController', ['$scope', '$rootScope', '$state', 'Forms', '$filter', '$translate', function ($scope, $rootScope, $state, Forms, $filter, $translate) {
  const $translateVar = $filter('translate');

  $scope.form = {};

  $scope.disabled = false;

  $scope.form.STATUS = 'Draft';

  $scope.form.ORGANIZATIONS = [{ ORG_ID: $rootScope.loggedUser.organization }];

  $scope.form.EBMEDS_USAGE = 1;

  $scope.form.CATEGORY = 'symptom';

  $scope.categories = [{ categoryName: 'Oirearviokysely', categoryValue: 'symptom' }, { categoryName: 'Terveystarkastus', categoryValue: 'health-check' }, { categoryName: 'Pitkaikaissairaudet', categoryValue: 'chronic-disease' }];

  $scope.statuses = [{ statusName: 'DRAFT', statusValue: 'Draft' }];

  $scope.disabledValidation = true;

  $scope.disabledApproval = true;

  $scope.saveForm = function () {
    if ($scope.formForm.$valid === false) {
      return;
    }

    Forms.createForm($scope.form, function (data, status) {
      // When successful, go to the new forms details page
      if (status < 400) {
        $scope.form.ORGANIZATIONS = [{ ORG_ID: $rootScope.loggedUser.organization, FORM_ID: data.NEW_ID }];
        $state.go('secured.formDetailsPage', { formid: data.NEW_ID });
      }
    });
  };

  // Go back
  $scope.goBack = function () {
    window.history.back();
  };
}]);